import React from "react";
import {
  MDBCard,
  MDBCardTitle,
  MDBCardText,
  MDBCardBody,
  MDBCardImage,
  MDBRow,
  MDBCol,
} from "mdb-react-ui-kit";
import { Link } from "react-router-dom";
import Skeleton from "@mui/material/Skeleton";
import Stack from "@mui/material/Stack";
import Box from "@mui/material/Box";

import RateIcon from "../rate-icon/RateIcon";

const ReqCard = (props) => {
  console.log("props");
  console.log(props);
  return (
    <>
      {props.title == "" ||
      props.img == "" ||
      props.desc1 == "" ||
      props.desc2 == "" ? (
        <MDBCard
          className="color-101010 hov-trans px-auto hov-shadow shadow-off card-border rec-card"
          style={{ maxWidth: "" }}
        >
          <MDBRow className="g-0  h-skeleton-inherti">
            <MDBCol className="col-5 col-sm-4 col-md-5">
              <Skeleton
                variant="rectangular"
                className="img-r-card "
                animation="wave"
              />
            </MDBCol>
            <MDBCol className="col-7 col-sm-8 col-md-7">
              <MDBCardBody className="me-2 mt-2 pt-1 p-0 text-end">
                <MDBCardText className="ps-2">
                  <Skeleton
                    className="skeleton-text1-rec"
                    variant="text"
                    sx={{ fontSize: "1rem" }}
                    width={120}
                    animation="wave"
                  />
                  <Skeleton
                    className="skeleton-text2-rec"
                    variant="text"
                    sx={{ fontSize: "0.6rem" }}
                    animation="wave"
                  />
                  <Skeleton
                    className="mt-0"
                    variant="text"
                    sx={{ fontSize: "0.6rem" }}
                    animation="wave"
                  />

                  <Skeleton
                    className="skeleton-gish-rec"
                    width={95}
                    height={30}
                    animation="wave"
                  />
                </MDBCardText>
              </MDBCardBody>
            </MDBCol>
          </MDBRow>
        </MDBCard>
      ) : (
        <a href={props.url} target={props.blog ? "_blank" : ""} className="">
          <MDBCard
            className="color-101010 hov-trans px-auto hov-shadow shadow-off card-border rec-card"
            style={{ maxWidth: "" }}
          >
            <MDBRow className="g-0  ">
              <MDBCol className="col-5 col-sm-4 col-md-5">
                <MDBCardImage
                  className="img-r-card "
                  src={props.img}
                  alt={props.title}
                  fluid
                />
              </MDBCol>
              <MDBCol className="col-7 col-sm-4 col-md-7">
                <MDBCardBody className="me-2 mt-2 pt-1 p-0 text-end">
                  <MDBCardTitle className="fw-bold fs-15 mb-1 fs-xs-15 ">
                    {props.title}
                  </MDBCardTitle>
                  <MDBCardText className=" mb-sm-2 mb-0 fs-14 fs-xs-14">
                    {props.desc1}
                  </MDBCardText>
                  <MDBCardText className=" fs-13 fs-xs-13 color-101010ab">
                    {props.desc2}
                  </MDBCardText>
                  <MDBCardText className="">
                    <RateIcon
                      cardrate={parseFloat(props.rate)}
                      isRec={1}
                      isAdd={0}
                      dir="ltr "
                    />
                  </MDBCardText>
                </MDBCardBody>
              </MDBCol>
            </MDBRow>
          </MDBCard>
        </a>
      )}
    </>
  );
};

export default ReqCard;
