import React, { useState, useEffect, useContext } from "react";
import { Link, useHistory } from "react-router-dom";
import { MDBBtn, MDBContainer, MDBRow } from "mdb-react-ui-kit";
import axios from "axios";

import { DomainContext, CookieContext } from "../../App";
import { validate } from "../login/validate";
import Footer from "../footer/Footer";
import MetaTagsSeo from "../meta-tags-seo/MetaTagsSeo";

import closeIcon from "../../images/header/close.png";
import logo from "../../images/home-body/logo gishniz.png";
import facebook_login from "../../images/facebook-login.png";
import google_login from "../../images/google-login.png";

const Signup = (props) => {
  const { state } = props.location;

  // for domain of Apis
  const domain = useContext(DomainContext);
  // cookie context
  const { cookieState, setCookieState } = useContext(CookieContext);

    // this 2 satae for Meta Tags
    const [MTagsCome, setMTagsCome] = useState(false);
    const [MetaData, setMetaData] = React.useState({});

  const [existErr, setExistErr] = useState();
  const [fillErr, setFillErr] = useState();
  const [serverErr, setServerErr] = useState();
  const [disable, setDisable] = useState(false);

  const [inputData, setInputData] = useState({
    fname: "",
    lname: "",
    email: "",
    username: "",
    password: "",
    isAccepted: false,
  });

  useEffect(() => {
    if (getCookie("MemId")) {
      props.history.push({
        pathname: "/members/profile",
      });
    }
  }, []);

  function getCookie(cname) {
    let name = cname + "=";
    let decodedCookie = decodeURIComponent(document.cookie);
    let ca = decodedCookie.split(";");
    for (let i = 0; i < ca.length; i++) {
      let c = ca[i];
      while (c.charAt(0) == " ") {
        c = c.substring(1);
      }
      if (c.indexOf(name) == 0) {
        return c.substring(name.length, c.length);
      }
    }
    return "";
  }

  const [errors, setErrors] = useState({});
  const [touched, setTouched] = useState({});

  const history = useHistory();

  useEffect(() => {
    setErrors(validate(inputData, "signup"));
  }, [inputData, touched]);

  const inputChangeHandler = (event) => {
    if (event.target.name === "isAccepted") {
      if (inputData.isAccepted) {
        setInputData({
          ...inputData,
          [event.target.name]: false,
        });
      } else {
        setInputData({
          ...inputData,
          [event.target.name]: true,
        });
      }
    } else {
      setInputData({ ...inputData, [event.target.name]: event.target.value });
    }
  };

  //api header
  const header = {
    ApiKey: "87e4d3a91a554a2196ae8bdf0bfdcdc9",
  };

  const submitHandler = (event) => {
    event.preventDefault();

    if (!Object.keys(errors).length) {
      // success
      // if (state) {
      //   props.history.push({
      //     pathname: "/introduce-business/introduce-business-thanks",
      //     state: state, // your data array of objects
      //   });
      // } else {

      setExistErr(false);
      setServerErr(false);
      setFillErr(false);

      const data = {
        Username: inputData.username,
        Email: inputData.email,
        Password: inputData.password,
        Lastname: inputData.lname,
        Firstname: inputData.fname,
      };
      console.log(data);
      axios
        .post(domain + "/api/member-register", data, {
          headers: header,
        })
        .then((response) => {
          setDisable(false);
          console.log(response.data);

          if (response.data.msg == "done") {
            setCookie("MemId", response.data.MemberDetail.MemId, 1);
            localStorage.setItem(
              "MemberDetail",
              JSON.stringify(response.data.MemberDetail)
            ); //set local
            setCookieState(response.data.MemberDetail.MemId);

            if (state == null) {
              props.history.push({
                pathname: "/",
                // state: response.data.OwnerDetail.OwnerId, // your data array of objects
              });
            } else {
              props.history.push({
                pathname: state,
              });
            }
          } else if (response.data.msg == "member-exist") {
            console.log("owner-exist");
            setExistErr(true);
          } else if (response.data.msg == "error") {
            console.log("error");
            setServerErr(true);
          } else if (response.data.msg == "fill-all") {
            console.log("fill-all");
            setFillErr(true);
          }
        });
      // }
    } else {
      setTouched({
        fname: true,
        lname: true,
        email: true,
        username: true,
        password: true,
        isAccepted: true,
      });
    }
  };

  //   setCookie
  function setCookie(cname, cvalue, exdays) {
    const d = new Date();
    d.setTime(d.getTime() + exdays * 24 * 60 * 60 * 1000);
    let expires = "expires=" + d.toUTCString();
    document.cookie = cname + "=" + cvalue + ";" + expires + ";path=/";
  }

  //api header
  const headerMeta = {
    ApiKey: "87e4d3a91a554a2196ae8bdf0bfdcdc9",
    Page: "register",
  };

  useEffect(() => {
    axios
      .get(domain + "/api/member/meta-login-register", { headers: headerMeta })
      .then((response) => {
        console.log("response.data");
        console.log(response.data);
        setMTagsCome(true);
        setMetaData(response.data);
      })
      .catch((error) => {
        // alert(error);
      });
  }, []);

  return (
    <>
      {MTagsCome ? <MetaTagsSeo data={MetaData} /> : ""}

      <div className="centerr">
        <div className="centerr d-none d-md-block">
          <Link to="/" className=" logo-login-a ">
            <img src={logo} alt="logo gishniz" className="logo-login-lap" />
          </Link>
        </div>

        <div className="login-container centerr">
          <MDBContainer className="">
            <MDBRow className="text-end">
              <Link to="/" className="a-close-img">
                <img
                  className="login-close-img me-0"
                  src={closeIcon}
                  alt="close icon"
                />
              </Link>
            </MDBRow>
            <MDBRow className="">
              <div className="col-12 text-center">
                <div className="centerr">
                  <Link to="/" className=" logo-login-a d-block d-md-none">
                    <img
                      src={logo}
                      alt="logo gishniz"
                      className="logo-login "
                    />
                  </Link>
                </div>

                <p className=" login-matn1 fs-xss-21 color-515151 fw-bold mt-3 mt-md-0 mb-2">
                  ورود به حساب کاربری
                </p>
                <p className="fs-xss-12 color-8d8c8c mb-4 pb-2 pb-md-0 login-matn2">
                  برای استفاده از امکانات سایت لطفا ثبت‌نام کنید.
                </p>
              </div>
              <div className="col-12 mt-1 padding-xss-32">
                <MDBBtn className="shadow-off account-login-btn account-signup-btn text-start">
                  ثبت‌نام با فیسبوک
                  <img
                    className="account-login-img"
                    src={facebook_login}
                    alt=""
                  />
                </MDBBtn>
                {/* <Facebook /> */}
                <MDBBtn className="shadow-off account-login-btn account-signup-btn text-start">
                  ثبت‌نام با گوگل
                  <img
                    className="account-login-img"
                    src={google_login}
                    alt=""
                  />
                </MDBBtn>
                {/* <Google /> */}

                <div className="centerr">
                  <div className="divider-login d-flex align-items-center">
                    <p className="text-center mb-0 divider-login-text fs-xss-12">
                      ثبت‌نام با نام کاربری
                    </p>
                  </div>
                </div>
                <div className="">
                  <form>
                    <input
                      name="fname"
                      value={inputData.fname}
                      onChange={inputChangeHandler}
                      className={
                        errors.fname && touched.fname
                          ? "input-login mb-md-1 mb-md-1 border-error"
                          : "input-login mb-md-1 mb-md-1"
                      }
                      type="text"
                      placeholder="نام"
                    />

                    {errors.fname && touched.fname && (
                      <span className="input-error">{errors.fname}</span>
                    )}

                    <input
                      name="lname"
                      value={inputData.lname}
                      onChange={inputChangeHandler}
                      // onFocus={inputFocusHanlder}
                      className={
                        errors.lname && touched.lname
                          ? "input-login mb-md-1 border-error mt-2"
                          : "input-login mb-md-1 mt-2"
                      }
                      type="text"
                      placeholder="نام خانوادگی"
                    />
                    {errors.lname && touched.lname && (
                      <span className="input-error">{errors.lname}</span>
                    )}

                    <input
                      name="email"
                      value={inputData.email}
                      onChange={inputChangeHandler}
                      // onFocus={inputFocusHanlder}
                      className={
                        errors.email && touched.email
                          ? "input-login mb-md-1 border-error mt-2"
                          : "input-login mb-md-1 mt-2"
                      }
                      type="email"
                      placeholder="ایمیل"
                    />
                    {errors.email && touched.email && (
                      <span className="input-error">{errors.email}</span>
                    )}

                    <input
                      name="username"
                      value={inputData.username}
                      onChange={inputChangeHandler}
                      // onFocus={inputFocusHanlder}
                      className={
                        errors.username && touched.username
                          ? "input-login mb-md-1 border-error mt-2"
                          : "input-login mb-md-1 mt-2"
                      }
                      type="text"
                      placeholder="نام کاربری"
                    />

                    {errors.username && touched.username && (
                      <span className="input-error">{errors.username}</span>
                    )}

                    <input
                      type="password"
                      maxLength="15"
                      name="password"
                      value={inputData.password}
                      onChange={inputChangeHandler}
                      // onFocus={inputFocusHanlder}
                      className={
                        errors.password && touched.password
                          ? "input-login border-error mt-2"
                          : "input-login mt-2"
                      }
                      placeholder="رمز عبور"
                    />
                    {errors.password && touched.password && (
                      <span className="input-error">{errors.password}</span>
                    )}

                    <div className="rightt">
                      <label className="container-s2 signup-checkbox">
                        <input
                          type="checkbox"
                          name="isAccepted"
                          value={inputData.isAccepted}
                          onChange={inputChangeHandler}
                        />
                        <span className="checkmark-s2"></span>
                        <span>
                          قوانین و مقررات سایت را مطالعه کردم و قبول دارم.
                        </span>
                      </label>
                      <br />
                      {/* {errors.isAccepted && touched.isAccepted && <span  className="mt-5">{errors.isAccepted}</span>} */}
                      {errors.isAccepted && touched.isAccepted && (
                        <span className="input-error mt-md-n3 mt-n2 pt-1">
                          {errors.isAccepted}
                        </span>
                      )}
                    </div>
                    {existErr && (
                      <span className="input-error">ایمیل تکراری است.</span>
                    )}
                    {serverErr && (
                      <span className="input-error">
                        خطا در ارتباط با سرور، لطفا دقایقی دیگر مجددا تلاش
                        نمایید.
                      </span>
                    )}
                    {fillErr && (
                      <span className="input-error">
                        لطفا همه موارد را پر کنید.
                      </span>
                    )}

                    <MDBBtn
                      disabled={disable}
                      onClick={submitHandler}
                      type="submit"
                      className="shadow-off hov-shadow-btn submit-login-btn text-center"
                    >
                      ثبت‌نام در گیشنیز
                    </MDBBtn>
                    <div className="d-flex justify-content-center mb-4 fs-14 fs-xss-14">
                      <p className="color-242b2e">حساب کاربری دارید؟ </p>
                      <Link className="color-5ae6a2" to="/login">
                        <p className="text-end me-2">ورود به حساب کاربری </p>
                      </Link>
                    </div>
                  </form>
                </div>
              </div>
            </MDBRow>
          </MDBContainer>
        </div>
      </div>
      <Footer />
    </>
  );
};

export default Signup;
