import React, { useState, useEffect } from "react";
import Header2 from "../../components/header/Header2";
import Headermobile from "../../components/header-mobile/Headermobile";
import Footer from "../../components/footer/Footer";
import { validate } from "../../components/login/validate";
import { MDBBtn } from "mdb-react-ui-kit";
import AlertSuccess from "../../components/Dashbord/AlertSuccess/AlertSuccess";
// rtl
import { createTheme, ThemeProvider } from "@mui/material/styles";
import TextField from "@mui/material/TextField";
import rtlPlugin from "stylis-plugin-rtl";
import { prefixer } from "stylis";
import { CacheProvider } from "@emotion/react";
import createCache from "@emotion/cache";
const theme = createTheme({
  direction: "rtl", // Both here and <body dir="rtl">
});
// Create rtl cache
const cacheRtl = createCache({
  key: "muirtl",
  stylisPlugins: [prefixer, rtlPlugin],
});

const Advertise = () => {
  const [showAlert, setshowAlert] = useState(false);
  // data
  const [data, setData] = useState({
    Business: "",
    Namee: "",
    Countryy: "",
    Cityy: "",
    Emaill: "",
    Phone: "",
  });
  const inputChangeHandler = (event) => {
    setData({ ...data, [event.target.name]: [event.target.value] });
  };
  // validate
  const [errors, setErrors] = useState({});
  const [touched, setTouched] = useState({});

  useEffect(() => {
    setErrors(validate(data, "advertise"));
  }, [data, touched]);

  const submitHandler = (event) => {
    event.preventDefault();

    if (!Object.keys(errors).length) {
      // success
      // props.history.push({
      //   pathname: "/",
      // });
      alertHandeler();
    } else {
      setTouched({
        Business: true,
        Countryy: true,
        Cityy: true,
        Emaill: true,
      });
    }
  };
  // alert
  const alertHandeler = () => {
    setshowAlert(true);
    setTimeout(() => {
      // After 3 seconds set the show value to false
      setshowAlert(false);
    }, 4000);
  };

  return (
    <>
      <div className="d-none d-md-block">
        <Header2 />
      </div>
      <div className="d-block d-md-none">
        <div className="fixed-header">
          <Headermobile />
        </div>
        <div className="box-under-header-res"></div>
      </div>
      {/* header */}
      <CacheProvider value={cacheRtl}>
        <ThemeProvider theme={theme}>
          <div className="gish-page-back">
            <div className="gish-con-tit">
              تبلیغات در <span className="color-5ae6a2"> گیشنیز</span>
            </div>
            <div className="gish-con-body">
              <p>
                برای کسب اطلاعات از پکیجهای تبلیغاتی گیشنیز، اطلاعات خود را وارد
                کنید. <br className="d-md-none" />
                تیم مارکتینگ گیشنیز بزودی با شما تماس می‌گیرد.
              </p>
              <div className="mt-4">
                {/* <div className="material mx-auto material-con mt-0">
            <input
              className={
                errors.Business && touched.Business
                  ? "input_custom rb-error"
                  : "input_custom mb-0"
              }
              type="text"
              required
              name="Business"
              value={data.Business}
              onChange={inputChangeHandler}
            />
            <span
              className={
                errors.Business && touched.Business
                  ? "borda borda-error con-right-0"
                  : "borda con-right-0"
              }
            ></span>
            <label className="lable_input_custom">نام کسب وکار</label>
          </div> */}
                <div>
                  <TextField
                    name="Business"
                    value={data.Business}
                    onChange={inputChangeHandler}
                    id="outlined-basic"
                    label="نام کسب وکار"
                    variant="outlined"
                    className={
                      errors.Business && touched.Business
                        ? "label-field-more7 custom-text-field custom-text-field-contact custom-text-field-error"
                        : "label-field-more7 custom-text-field custom-text-field-contact mb-0"
                    }
                  />
                </div>
                {errors.Business && touched.Business && (
                  <span className="input-error input-error-con me-1">
                    {errors.Business}
                  </span>
                )}

                {/* <div className="material mx-auto material-con mt-3">
                  <input
                    className={
                      errors.Namee && touched.Namee
                        ? "input_custom rb-error"
                        : "input_custom mb-0"
                    }
                    type="text"
                    required
                    name="Namee"
                    value={data.Namee}
                    onChange={inputChangeHandler}
                  />
                  <span
                    className={
                      errors.Namee && touched.Namee
                        ? "borda borda-error con-right-0"
                        : "borda con-right-0"
                    }
                  ></span>
                  <label className="lable_input_custom">
                    نام و نام‌خانوادگی
                  </label>
                </div> */}
                <div className="mt-3">
                  <TextField
                    name="Namee"
                    value={data.Namee}
                    onChange={inputChangeHandler}
                    id="outlined-basic"
                    label="نام و نام‌خانوادگی"
                    variant="outlined"
                    className={
                      errors.Namee && touched.Namee
                        ? "label-field-more10 custom-text-field custom-text-field-contact custom-text-field-error"
                        : "label-field-more10 custom-text-field custom-text-field-contact mb-0"
                    }
                  />
                </div>
                {errors.Namee && touched.Namee && (
                  <span className="input-error input-error-con me-1">
                    {errors.Namee}
                  </span>
                )}

                {/* <div className="material mx-auto material-con mt-3">
                  <input
                    className={
                      errors.Countryy && touched.Countryy
                        ? "input_custom rb-error"
                        : "input_custom mb-0"
                    }
                    type="text"
                    required
                    name="Countryy"
                    value={data.Countryy}
                    onChange={inputChangeHandler}
                  />
                  <span
                    className={
                      errors.Countryy && touched.Countryy
                        ? "borda borda-error con-right-0"
                        : "borda con-right-0"
                    }
                  ></span>
                  <label className="lable_input_custom">کشور</label>
                </div> */}
                <div className="">
                  <TextField
                    type="text"
                    name="Countryy"
                    value={data.Countryy}
                    onChange={inputChangeHandler}
                    id="outlined-basic"
                    label="کشور"
                    variant="outlined"
                    className={
                      errors.Countryy && touched.Countryy
                        ? "label-field-less1 custom-text-field custom-text-field-contact custom-text-field-error mt-3"
                        : "label-field-less1 custom-text-field custom-text-field-contact mb-0 mt-3"
                    }
                  />
                </div>
                {errors.Countryy && touched.Countryy && (
                  <span className="input-error input-error-con me-1">
                    {errors.Countryy}
                  </span>
                )}

                {/* <div className="material mx-auto material-con mt-3">
                  <input
                    className={
                      errors.Cityy && touched.Cityy
                        ? "input_custom rb-error"
                        : "input_custom mb-0"
                    }
                    type="text"
                    required
                    name="Cityy"
                    value={data.Cityy}
                    onChange={inputChangeHandler}
                  />
                  <span
                    className={
                      errors.Cityy && touched.Cityy
                        ? "borda borda-error con-right-0"
                        : "borda con-right-0"
                    }
                  ></span>
                  <label className="lable_input_custom">شهر</label>
                </div> */}
                <div className="">
                  <TextField
                    type="text"
                    name="Cityy"
                    value={data.Cityy}
                    onChange={inputChangeHandler}
                    id="outlined-basic"
                    label="شهر"
                    variant="outlined"
                    className={
                      errors.Cityy && touched.Cityy
                        ? "label-field-less1 custom-text-field custom-text-field-contact custom-text-field-error mt-3"
                        : "label-field-less1 custom-text-field custom-text-field-contact mb-0 mt-3"
                    }
                  />
                </div>
                {errors.Cityy && touched.Cityy && (
                  <span className="input-error input-error-con me-1">
                    {errors.Cityy}
                  </span>
                )}

                {/* <div className="material mx-auto material-con mt-3">
                  <input
                    className={
                      errors.Emaill && touched.Emaill
                        ? "input_custom rb-error"
                        : "input_custom mb-0"
                    }
                    type="email"
                    required
                    name="Emaill"
                    value={data.Emaill}
                    onChange={inputChangeHandler}
                  />
                  <span
                    className={
                      errors.Emaill && touched.Emaill
                        ? "borda borda-error con-right-0"
                        : "borda con-right-0"
                    }
                  ></span>
                  <label className="lable_input_custom">ایمیل</label>
                </div> */}
                <div className="">
                  <TextField
                    type="email"
                    name="Emaill"
                    value={data.Emaill}
                    onChange={inputChangeHandler}
                    id="outlined-basic"
                    label="ایمیل"
                    variant="outlined"
                    className={
                      errors.Emaill && touched.Emaill
                        ? "label-field-less custom-text-field custom-text-field-contact custom-text-field-error mt-3"
                        : "label-field-less custom-text-field custom-text-field-contact mb-0 mt-3"
                    }
                  />
                </div>
                {errors.Emaill && touched.Emaill && (
                  <span className="input-error input-error-con me-1">
                    {errors.Emaill}
                  </span>
                )}

                {/* <div className="material mx-auto material-con mt-3">
                  <input
                    className={
                      errors.Phone && touched.Phone
                        ? "input_custom rb-error"
                        : "input_custom mb-0"
                    }
                    type="text"
                    required
                    name="Phone"
                    value={data.Phone}
                    onChange={inputChangeHandler}
                  />
                  <span
                    className={
                      errors.Phone && touched.Phone
                        ? "borda borda-error con-right-0"
                        : "borda con-right-0"
                    }
                  ></span>
                  <label className="lable_input_custom">تلفن</label>
                </div> */}
                <div className="mt-3">
                  <TextField
                  error
                    name="Phone"
                    value={data.Phone}
                    onChange={inputChangeHandler}
                    id="outlined-basic"
                    label="تلفن"
                    variant="outlined"
                    className={
                      errors.Phone && touched.Phone
                        ? "label-field-less custom-text-field custom-text-field-contact custom-text-field-error"
                        : "label-field-less custom-text-field custom-text-field-contact mb-0"
                    }
                  />
                </div>
              </div>
              <MDBBtn
                // to=""
                className="hov-shadow-btn con-btn"
                onClick={submitHandler}
              >
                ثبت
              </MDBBtn>
            </div>
          </div>
        </ThemeProvider>
      </CacheProvider>
      <Footer />
      <AlertSuccess
        showAlert={showAlert}
        text={" اطلاعات شما با موفقیت ثبت شد."}
      />
    </>
  );
};

export default Advertise;
