import React from "react";
import { Link } from "react-router-dom";
import {
  MDBCard,
  MDBCardTitle,
  MDBCardText,
  MDBCardBody,
  MDBCardImage,
  MDBRow,
  MDBCol,
} from "mdb-react-ui-kit";
import "./result-card.css";
import Skeleton from "@mui/material/Skeleton";
import RateIcon from "../rate-icon/RateIcon";
import Avatar from "@mui/material/Avatar";

const ResultCard = (props) => {
  return (
    <Link to={props.url} className="res-card-a w-auto">
      <MDBCard className="res-card hov-shadow">
        <MDBRow className="g-0">
          <MDBCol md="4">
            { props.pageChanged ? (
              <Skeleton
                variant="rectangular"
                className="res-card-img "
                animation="wave"
              />
            ) : (
              <MDBCardImage
                className="res-card-img"
                src={props.img}
                alt={props.title}
                fluid
              />
            )}
          </MDBCol>
          <MDBCol md="8">
            <MDBCardBody>
              <MDBCardTitle className="res-card-tit">
                { props.pageChanged ? (
                  <Skeleton
                    className="skeleton-text1-rec"
                    variant="text"
                    sx={{ fontSize: "1.5rem" }}
                    width={120}
                    animation="wave"
                  />
                ) : (
                  <p className="mb-0">{props.title}</p>
                )}
                {/* { props.pageChanged ? (
                  <Skeleton
                    variant="circular"
                    width={22}
                    height={22}
                    animation="wave"
                  >
                    <Avatar animation="wave" />
                  </Skeleton>
                ) : (
                  <img
                    className="res-card-tit-img"
                    src={props.icon}
                    alt="country"
                  />
                )} */}
              </MDBCardTitle>
              {  props.pageChanged ? (
                <Skeleton
                  className="skeleton-text1-rec"
                  variant="text"
                  // sx={{ fontSize: "1.5rem" }}
                  width={120}
                  animation="wave"
                />
              ) : (
                <MDBCardText className="res-card-text1">
                  {props.desc1}
                </MDBCardText>
              )}
              { props.pageChanged ? (
                <Skeleton
                  className="skeleton-text1-rec"
                  variant="text"
                  sx={{ fontSize: "0.6rem" }}
                  width={120}
                  animation="wave"
                />
              ) : (
                <MDBCardText className="res-card-text2">
                  {props.city}
                </MDBCardText>
              )}
              {/* { props.pageChanged ? (
                <Skeleton
                  className="skeleton-text1-rec"
                  variant="text"
                  sx={{ fontSize: "0.6rem" }}
                  width={120}
                  animation="wave"
                />
              ) : (
                <MDBCardText className="res-card-text2">
                  {props.country}
                </MDBCardText>
              )} */}

              { props.pageChanged ? (
                <Skeleton
                  className="skeleton-text1-rec"
                  variant="text"
                  width={120}
                  sx={{ fontSize: "1.2rem" }}
                  animation="wave"
                />
              ) : (
                <>
                  <div className="res-card-box2">
                    <RateIcon
                      //   cardrate={parseFloat(props.rate)}
                      cardrate={parseFloat(props.rate)}
                      isRec={0}
                      isAdd={1}
                      dir="ltr "
                    />
                  </div>
                  <br />
                </>
              )}

              {/* { props.pageChanged ? (
                <>
                  <Skeleton
                    className="skeleton-text1-rec"
                    variant="text"
                    sx={{ fontSize: "1rem" }}
                    animation="wave"
                  />
                  <br />
                </>
              ) : (
                <div className="res-card-box">
                  <p>ایرانی</p>

                  <p>ایتالیایی</p>

                  <p>گیلانی</p>
                </div>
              )} */}

              { props.pageChanged ? (
                <>
                  <Skeleton
                    className="skeleton-text1-rec"
                    variant="text"
                    sx={{ fontSize: "0.5rem" }}
                    animation="wave"
                  />
                  <Skeleton
                    className="skeleton-text1-rec mb-n2"
                    variant="text"
                    sx={{ fontSize: "0.5rem" }}
                    animation="wave"
                  />
                </>
              ) : (
                <MDBCardText className="res-card-text3 ">
                  {props.tozih}
                  ... <span>بیشتر</span>
                </MDBCardText>
              )}
            </MDBCardBody>
          </MDBCol>
        </MDBRow>
      </MDBCard>
    </Link>
  );
};

export default ResultCard;
