import React, { useEffect, useState, useContext } from "react";
import {
  MDBContainer,
  MDBRow,
  MDBListGroup,
  MDBListGroupItem,
} from "mdb-react-ui-kit";
import axios from "axios";
import { Link } from "react-router-dom";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import Divider from "@mui/material/Divider";
import ClickAwayListener from "@mui/material/ClickAwayListener";
import Skeleton from "@mui/material/Skeleton";
// import "./Header.css";
import Box from "@mui/material/Box";
import { DomainContext, CookieContext } from "../../App";
import Inputs from "./Inputs";

import logo from "../../images/home-body/logo gishniz.png";
import kharid from "../../images/header/خرید-سفید.png";
import resturan from "../../images/header/رستوران-سفید.png";
import pezeshki from "../../images/header/پزشکی -سفید.png";
import vekalat from "../../images/header/وکالت-سفید.png";

import imageLogin from "../../images/login img/user gray.png";
import imageLoginwhite from "../../images/login img/user.png";
import imageLogout from "../../images/login img/log-out gray.png";
import nextLogin from "../../images/login img/play (1).png";
import account from "../../images/login img/account.png";
//
const Header = (props) => {
  // cookie context
  const { cookieState, setCookieState } = useContext(CookieContext);

  const [showmenu, setshowmenu] = useState(false);

  function getCookie(cname) {
    let name = cname + "=";
    let decodedCookie = decodeURIComponent(document.cookie);
    let ca = decodedCookie.split(";");
    for (let i = 0; i < ca.length; i++) {
      let c = ca[i];
      while (c.charAt(0) == " ") {
        c = c.substring(1);
      }
      if (c.indexOf(name) == 0) {
        return c.substring(name.length, c.length);
      }
    }
    return "";
  }
  // delete all cookies logggggg outttt
  function deleteAllCookies() {
    localStorage.clear();

    // var cookies = document.cookie.split(";");

    // for (var i = 0; i < cookies.length; i++) {
    //   var cookie = cookies[i];
    //   var eqPos = cookie.indexOf("=");
    //   var name = eqPos > -1 ? cookie.substr(0, eqPos) : cookie;
    //   document.cookie = name + "=;expires=Thu, 01 Jan 1970 00:00:00 GMT";
    // }
    document.cookie = "MemId=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;";
    setCookieState("");

    setshowmenu(!showmenu);
  }

  const handleShow = () => {
    setshowmenu((prev) => !prev);
  };

  const handleShowAway = () => {
    setshowmenu(false);
  };

  return (
    <div
      className="mb-30 head-bg d-none d-md-block"
      style={{ backgroundImage: `url(${props.data.CoverPhoto})` }}
    >
      <div className="header-color">
        <nav className="">
          <MDBContainer fluid className="pt-34 px-5 pb-5">
            <MDBRow className="px-2 mx-0 align-items-center">
              <div className="col-6 p-0">
                <div className="width-53px">
                  <a
                    href="https://blog.gishniz.com/"
                    target="_blank"
                    className=" fs-18 color-5ae6a2 width-53px"
                  >
                    <p className="sabt-hov mb-0 ">بلاگ</p>
                  </a>
                </div>
              </div>
              <div className="col-6 p-0 d-flex justify-content-end  align-items-center">
                <a
                  href="https://biz.gishniz.com/choose-owner"
                  target="_blank"
                  className="color-5ae6a2 fs-18 align-items-center ps-1 ms-4 "
                >
                  <p className="sabt-hov mb-0">ثبت کسب‌وکار</p>
                </a>
                <ClickAwayListener onClickAway={handleShowAway}>
                  <Box>
                    {getCookie("MemId") &&
                    JSON.parse(localStorage.getItem("MemberDetail")) ? (
                      <>
                        <div
                          className={
                            showmenu
                              ? "select_login_sucess select_login_sucess_color"
                              : "select_login_sucess"
                          }
                          // onMouseOver={() => setshowmenu(true)}
                          // onMouseOut={() => setshowmenu(false)}

                          onClick={handleShow}
                          // onMouseDown ={() => setshowmenu(false)}
                        >
                          <img
                            className="imageLogin_success_header"
                            src={imageLoginwhite}
                            alt=""
                          />
                          <img
                            className={
                              showmenu
                                ? "nextLogin nextLogin-rotate"
                                : "nextLogin nextLogin-rotateback"
                            }
                            src={nextLogin}
                            alt=""
                          />
                        </div>
                        {showmenu && (
                          <List
                            onMouseOver={() => setshowmenu(true)}
                            onMouseOut={() => setshowmenu(false)}
                            className="box_show_info_userlogin"
                            component="nav"
                            aria-label="mailbox folders"
                          >
                            <ListItem button>
                              <img
                                className="img20x20"
                                // src={imageLogin}
                                src={
                                  JSON.parse(
                                    localStorage.getItem("MemberDetail")
                                  ).Avator
                                }
                                alt="icon"
                              />
                              {
                                JSON.parse(localStorage.getItem("MemberDetail"))
                                  .Fullname
                              }
                            </ListItem>
                            <Divider />
                            <Link to={"/members/profile"}>
                              <ListItem button>
                                <img
                                  className="img12x12"
                                  src={account}
                                  alt=""
                                />
                                مشاهده پروفایل
                              </ListItem>
                            </Link>
                            <Divider />
                            <ListItem onClick={deleteAllCookies} button>
                              <img
                                className="img20x20"
                                src={imageLogout}
                                alt=""
                              />
                              خروج
                            </ListItem>
                          </List>
                        )}
                      </>
                    ) : (
                      <>
                        {/* // <select className="select_login_sucess">
                      //   <option></option>
                      //   <option>{getCookie("userName")}</option>
                      //   <option>مشاهده پروفایل</option>
                      //   <option>خروج</option>
                      // </select> */}
                        <Link
                          className="text-white bg-head-btn fs-18"
                          to="/login"
                        >
                          وارد شوید
                        </Link>
                      </>
                    )}
                  </Box>
                </ClickAwayListener>
                {/* </MDBBtn> */}
              </div>
            </MDBRow>
          </MDBContainer>
        </nav>

        <div className="container text-align-center pt-4 mb-65">
          <a className="" href="/">
            <img
              src={props.data.HeaderLogo.Link}
              // src={logo}

              alt={props.data.HeaderLogo.Title}
              title={props.data.HeaderLogo.Title}
              height="60px"
              className="mt-2"
            />
          </a>
          <p className="mt-10 text-white fs-16 f">
            کسب‌وکارهای فارسی زبان اطرافت را پیدا کن
          </p>
        </div>
        <MDBContainer>
          <MDBRow>
            <div className="mb-65">
              <Inputs data={props.data.CategoriesForSearch} />
            </div>
          </MDBRow>
        </MDBContainer>
        <div className="  ">
          <MDBListGroup
            horizontal
            className="justify-content-center px-0 mt-15"
          >
            {props.data.CategoriesOnHeader.map((item) =>
              props.dataCome ? (
                <MDBListGroupItem
                  className="text-align-center py-0"
                  key={item.CId}
                >
                  <Link className="a-list fs-15" to={item.Url}>
                    <img
                      className="img-list mb-1"
                      src={item.Icon}
                      alt={item.ShortTitle}
                    />
                    <br />
                    {item.ShortTitle}
                  </Link>
                </MDBListGroupItem>
              ) : (
                <MDBListGroupItem className="text-align-center py-0">
                  <Link className="a-list fs-15">
                    <Skeleton
                      variant="circular"
                      className=""
                      width={50}
                      height={50}
                      animation="wave"
                    />
                    {/* <br /> */}
                    <Skeleton
                      className="mx-0 mt-3"
                      height={12}
                      animation="wave"
                    />
                  </Link>
                </MDBListGroupItem>
              )
            )}
            {/* <MDBListGroupItem className="text-align-center py-0">
              <Link className="a-list fs-15" to="/result">
                <img
                  className="img-list mb-1"
                  src={kharid}
                  alt="مرکز خرید و فروشگاه"
                />
                <br />
                خرید{" "}
              </Link>
            </MDBListGroupItem>

            <MDBListGroupItem className="text-align-center py-0">
              <Link className="a-list fs-15" to="/result">
                <img
                  className="img-list mb-1"
                  src={resturan}
                  alt=" رستوران و کافی‌شاپ"
                />
                <br />
                رستوران
              </Link>
            </MDBListGroupItem>
            <MDBListGroupItem className="text-align-center py-0">
              <Link className="a-list fs-15" to="/result">
                <img
                  className="img-list mb-1"
                  src={pezeshki}
                  alt=" پزشکی، مشاوره و سلامت"
                />
                <br />
                پزشکی
              </Link>
            </MDBListGroupItem>
            <MDBListGroupItem className="text-align-center py-0">
              <Link className="a-list fs-15" to="/members/profile">
                <img className="img-list mb-1 " src={vekalat} alt="وکالت" />
                <br />
                وکالت
              </Link>
            </MDBListGroupItem> */}
          </MDBListGroup>
        </div>
      </div>
    </div>
    // <div className="">
    //   hiiiiiiiiiiii
    //   <img src={props.data.CoverPhoto} alt="" />
    // </div>
  );
};


export default Header;
