import React from "react";
import {
  MDBCard,
  MDBCardImage,
  MDBRow,
  MDBCol,
  MDBIcon,
} from "mdb-react-ui-kit";
import Skeleton from "@mui/material/Skeleton";
import Avatar from "@mui/material/Avatar";

const CurrencyCard = (props) => {
  return (
    // <a href="" className="">
    <MDBCard className="continter mt-sm-3 mt-2 shadow-off currency-card color-101010 ">
      <MDBRow className="g-0 align-items-center">
        {props.title == "" ? (
          <div className="d-flex align-items-center px-3">
            <Skeleton
              variant="circular"
              width={32}
              height={30}
              animation="wave"
            >
              <Avatar animation="wave" />
            </Skeleton>
            <Skeleton className="mt-1 skeleton-currency" animation="wave" />
          </div>
        ) : (
          <>
            <MDBCol className="col-8 text-end fs-xs-15">
              <MDBCardImage
                className="curr-img me-3 ms-1"
                // src={}
                src={props.icon}
                alt={props.title}
                fluid
              />{" "}
              {props.title}
            </MDBCol>
            {/* <MDBCol className="col-2  text-center align-items-center  fs-xs-14">
              {props.isRed ? (
                <span className="color-ff0505">
                  <MDBIcon fas icon="caret-down fa-lg color-ff0505" /> 0/2
                </span>
              ) : (
                <span className="color-3ca539">
                  <MDBIcon fas icon="caret-up fa-lg " /> 0/2
                </span>
              )}
            </MDBCol> */}
            <MDBCol className="col-4 ps-3 text-start align-items-center fs-xs-14">
              <span>{props.price}</span>
            </MDBCol>
          </>
        )}
      </MDBRow>
    </MDBCard>
    // </a>
  );
};

export default CurrencyCard;
