import React from "react";
import { useState, createContext , useEffect } from "react";
import Layout from "./layout/Layout";

export const DomainContext = createContext();

const App = () => {
  const [domain, setDomain] = useState("https://api.gishniz.com");
  // const [domain, setDomain] = useState("https://api.gishnizapp.ir");

  const [cookieState, setCookieState] = useState(getCookie("MemId"));
  const value = { cookieState, setCookieState };

  function getCookie(cname) {
    let name = cname + "=";
    let decodedCookie = decodeURIComponent(document.cookie);
    let ca = decodedCookie.split(";");
    for (let i = 0; i < ca.length; i++) {
      let c = ca[i];
      while (c.charAt(0) == " ") {
        c = c.substring(1);
      }
      if (c.indexOf(name) == 0) {
        return c.substring(name.length, c.length);
      }
    }
    return "";
  }


  return (
    <CookieContext.Provider value={value}>
      <DomainContext.Provider value={domain}>
        <Layout />
      </DomainContext.Provider>
    </CookieContext.Provider>
  );
};

export const CookieContext = React.createContext({
  cookieState: "",
  setCookieState: () => {},
});

export default App;
