import React, { useState, useEffect, useContext } from "react";
import axios from "axios";
import { MDBBtn } from "mdb-react-ui-kit";
import TextField from "@mui/material/TextField";
import Autocomplete from "@mui/material/Autocomplete";
import Box from "@mui/material/Box";
import { Link } from "react-router-dom";
import { DomainContext } from "../../App";
import { useHistory } from "react-router-dom";
import "./Header.css";
import catImg from "../../images/header/category.png";
import searchImg from "../../images/header/search.png";
import searchBlack from "../../images/header/search black.png";

const Inputs = (props) => {
  // for domain of Apis
  const domain = useContext(DomainContext);

  // const [inputData, setInputData] = useState({
  //   whatInput: "",
  //   whereInput: "",
  // });
  const [keyTime, setKeyTime] = useState("");
  const [keyTime2, setKeyTime2] = useState("");

  const [whatInput, setWhatInput] = useState("");
  const [whereInput, setWhereInput] = useState("");
  const [bizList, setBizList] = useState([]);
  const [catList, setCatList] = useState([]);

  const [locList, setLocList] = useState([]);

  const [listComes, setListComes] = useState(false);
  const [listWhereComes, setListWhereComes] = useState(false);

  const [kojaValue, setKojaValue] = useState(null);
  const [kojaInputValue, setKojaInputValue] = useState("");

  const [chiValue, setChiValue] = useState(null);
  const [chiInputValue, setChiInputValue] = useState("");

  // const inputChangeHandler = (event) => {
  //   setInputData({ ...inputData, [event.target.name]: event.target.value });
  // };
  var go =
    "/search?find_desc=" +
    whatInput.replace(/\s+/g, "-") +
    "&find_loc=" +
    whereInput.replace(/\s+/g, "-") +
    "&cat_id=&find_cat=";

  //api header
  const header = {
    ApiKey: "87e4d3a91a554a2196ae8bdf0bfdcdc9",
  };

  const apiWhat = () => {
    if (whatInput.length > 2) {
      setCatList([]);
      setBizList([]);

      setListComes(false);

      axios
        .post(
          domain + "/api/search/what",
          { txtWhat: whatInput },
          {
            headers: header,
          }
        )
        .then((response) => {
          console.log(response.data);
          const arr = [...response.data.BizList, ...response.data.CatList];
          setCatList(arr);
        })
        .catch((error) => {
          // alert(error);
        });
    } else {
      setCatList([]);
    }
  };

  useEffect(() => {
    if (whatInput.length > 2) {
      setCatList([]);
      setBizList([]);

      setListComes(false);

      clearTimeout(keyTime2);
      setKeyTime2(setTimeout(apiWhat, 1500));
    }
  }, [whatInput]);

  const apiWhere = () => {
    if (whereInput.length > 2) {
      setListWhereComes(false);
      setLocList([]);

      axios
        .post(
          domain + "/api/search/where",
          { txtWhere: whereInput },
          {
            headers: header,
          }
        )
        .then((response) => {
          console.log("where log");
          console.log(response.data);
          setLocList(response.data.Locations);
        })
        .catch((error) => {
          // alert(error);
        });
    } else {
      setLocList([]);
    }
  };

  useEffect(() => {
    if (whereInput.length > 2) {
      setListWhereComes(false);

      clearTimeout(keyTime);
      setKeyTime(setTimeout(apiWhere, 1500));
    } else {
      setLocList([]);
    }
  }, [whereInput]);

  // const options = props.data.map((option) => {
  const options = catList.map((option) => {
    if (option.BId) {
      const op = option.Title;
      return {
        op: "آخرین جستجوها",
        ...option,
      };
    } else {
      const op = option.Title;
      return {
        op: "دسته‌بندی",
        ...option,
      };
    }
  });
  const [removeMask, setRemoveMask] = useState(false);
  const history = useHistory();

  const handleKeyDown = (event) => {
    console.log("eeeeeeeeeeeeeeeerrrrrrrrrrrrrr");

    if (event.key === "Enter") {
      history.push(go);
      console.log("eeeee11111111111111");
    }
  };

  return (
    <div className=" mt-2">
      <div className="Searchinput">
        {/* <button onClick={handleKeyDown}>sdsd</button> */}
        {/* <div className="first-input-what-container"> */}
        {/* <TextField
            name="first-input-what"
            type="text"
            className=""
            placeholder="دنبال چی می‌گردی؟"
          /> */}

        <div className="first-input-what-container relative auto1 auto-g">
          <Autocomplete
            onKeyDown={handleKeyDown}
            value={chiValue}
            onChange={(event, newValue) => {
              setChiValue(newValue);
              console.log("newValue:");
              console.log(newValue);
              // newValue.Title,
            }}
            inputValue={chiInputValue}
            onInputChange={(event, newInputValue) => {
              console.log("newInputValue:");
              console.log(newInputValue);
              var result;
              if (newInputValue == "undefined") {
                result = chiInputValue;
                console.log("11");
              } else {
                result = newInputValue;
                console.log("22");
              }
              console.log("result:");
              console.log(result);

              setChiInputValue(result);
              setWhatInput(result);
            }}
            // open={true}
            disableClearable
            freeSolo={true}
            className=""
            disablePortal
            openOnFocus={false}
            id="grouped-demo"
            // options={whatList}
            filterOptions={(options, state) => options}
            options={
              whatInput.length > 2
                ? options.sort((a, b) => -b.op.localeCompare(a.op))
                : nullList
            }
            groupBy={(option) => option.op}
            getOptionLabel={(option) => option.Title}
            sx={{
              width: 300,
              fontFamily: "inherit",
            }}
            renderOption={(props, option) => (
              <Box
                component="li"
                sx={{ "& > img": { ml: 2, flexShrink: 0 } }}
                className="fs-4vw"
                {...props}
              >
                {option.BId ? (
                  <Link to={option.Url} className="width-100">
                    <div className="position-relative d-flex div-auto-g1 color-101010">
                      <img
                        src={searchBlack}
                        alt="search black"
                        className=" ms-2  autocomplete-search-lap"
                      />
                      <div className="last1-auto">{option.Title}</div>
                      <div className="last2-auto">{option.Location}</div>
                    </div>
                  </Link>
                ) : (
                  <Link
                    to={option.Url.replace(/({loc})/g, whereInput)}
                    className="width-100"
                  >
                    <div className="position-relative d-flex div-auto-g2 color-101010">
                      <img
                        src={searchBlack}
                        alt="search black"
                        className=" ms-2  autocomplete-search-lap"
                      />
                      {option.Title}
                    </div>
                  </Link>
                )}
              </Box>
            )}
            renderInput={(params) => (
              <div className="d-flex">
                <TextField
                  className=""
                  {...params}
                  placeholder="دنبال چی می‌گردی؟"
                  name="whatInput"
                  value={whatInput}
                  onChange={(event) => {
                    setWhatInput(event.target.value);
                    if (whatInput.length < 1) {
                      setCatList([]);
                    }
                  }}
                >
                  {" "}
                </TextField>
              </div>
            )}
          />
        </div>
        {/* </div> */}

        <div className="category-input-container relative auto1">
          <Autocomplete
            onKeyDown={handleKeyDown}
            value={kojaValue}
            onChange={(event, newValue) => {
              console.log("newValue:");
              console.log(newValue);
              setKojaValue(newValue);
              // newValue.Title,
            }}
            inputValue={kojaInputValue}
            onInputChange={(event, newInputValue) => {
              console.log("newInputValue:");
              console.log(newInputValue);
              var result;
              if (newInputValue == "undefined") {
                result = kojaInputValue.replace(/[^A-Za-z\s,]/gi, "");
                console.log("11111111");
              } else {
                console.log("22");
                result = newInputValue.replace(/[^A-Za-z\s,]/gi, "");
              }
              console.log("result:");
              console.log(result);
              setKojaInputValue(result);
              setWhereInput(result);
            }}
            filterOptions={(options, state) => options}
            freeSolo={true}
            // open={true}
            disableClearable
            className=""
            disablePortal
            openOnFocus={false}
            id="auto-koja"
            options={whereInput.length > 2 ? locList : nullList}
            getOptionLabel={(option) => option.Title}
            sx={{
              width: 300,
              fontFamily: "inherit",
            }}
            // renderOption={(props, option) => (
            //   <div className="position-relative d-flex div-auto-g1 color-101010">{option.Title}</div>
            // )}
            renderInput={(params) => (
              <div className="d-flex">
                {/* <img src={catImg} className="icon-inputs position-absolute" alt="category img" /> */}
                <TextField
                  className=""
                  {...params}
                  placeholder="کجا؟(نام شهر به زبان انگلیسی)"
                  name="whereInput"
                  value={whereInput}
                  onChange={(event) => {
                    // const result = event.target.value.replace(
                    //   /[^A-Za-z\s,]/gi,
                    //   ""
                    // );

                    setWhereInput(event.target.value);
                  }}
                >
                  {" "}
                </TextField>
              </div>
            )}
          />
        </div>

        <Link to={go}>
          <MDBBtn className="search-submit-btn">
            <img src={searchImg} className="search-img" alt="search img" />
          </MDBBtn>
        </Link>
      </div>
    </div>
  );
};

const nullList = [];

export default Inputs;
