import React, { useEffect, useContext } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import MetaTags from "react-meta-tags";
import MetaTagsSeo from "../meta-tags-seo/MetaTagsSeo";

import ScrollCategory from "../scroll-category/ScrollCategory";
import Application from "../application/Application";
import RecCard from "../rec-card/RecCard";
import Rec2Card from "../rec2-card/Rec2Card";
import OverlayCard from "../overlay-card/OverlayCard";
import CurrencyCard from "../currency-card/CurrencyCard";
import CardCarousel from "../card-carousel/CardCarousel";

import kharid from "../../images/home-body/مرکز خرید و فروشگاه.png";
import tabliq from "../../images/home-body/تبلیغات.png";
import arz from "../../images/home-body/قیمت ارز.png";
import arzDigital from "../../images/home-body/ارز دیجیتال.png";
import blog from "../../images/home-body/blog.png";
import kasbokar from "../../images/home-body/کسب و کار های اطراف.png";
import newkasbokar from "../../images/home-body/تازه‌ترین های بیزنس.png";
import presentation from "../../images/home-body/presentation.png";
import { useState } from "react";

import { DomainContext } from "../../App";

const HomeBody = () => {
  // for domain of Apis
  const domain = useContext(DomainContext);
  //
  const [latestbiz, setLatestbiz] = useState([
    {
      Title: "",
      CatTitle: "",
      Loaction: "",
      Rate: "",
      PhotoPath: "",
    },
    {
      Title: "",
      CatTitle: "",
      Loaction: "",
      Rate: "",
      PhotoPath: "",
    },
    {
      Title: "",
      CatTitle: "",
      Loaction: "",
      Rate: "",
      PhotoPath: "",
    },
    {
      Title: "",
      CatTitle: "",
      Loaction: "",
      Rate: "",
      PhotoPath: "",
    },
  ]);

  const [nearbybiz, setNearbybiz] = useState([
    {
      Title: "",
      CatTitle: "",
      Location: "",
      Rate: "",
      PhotoPath: "",
      Url: "",
    },
    {
      Title: "",
      CatTitle: "",
      Location: "",
      Rate: "",
      PhotoPath: "",
      Url: "",
    },
    {
      Title: "",
      CatTitle: "",
      Location: "",
      Rate: "",
      PhotoPath: "",
      Url: "",
    },
  ]);
  const [bizrandomTit, setBizrandomTit] = useState({});
  const [bizrandomcat, setBizrandomcat] = useState([
    {
      Title: "",
      CatTitle: "",
      Loaction: "",
      Rate: "",
      PhotoPath: "",
      Url: "",
    },
    {
      Title: "",
      CatTitle: "",
      Loaction: "",
      Rate: "",
      PhotoPath: "",
      Url: "",
    },
    {
      Title: "",
      CatTitle: "",
      Loaction: "",
      Rate: "",
      PhotoPath: "",
      Url: "",
    },
    {
      Title: "",
      CatTitle: "",
      Loaction: "",
      Rate: "",
      PhotoPath: "",
      Url: "",
    },
  ]);

  const [categories, setCategories] = useState([
    { Title: "", Icon: "" },
    { Title: "", Icon: "" },
    { Title: "", Icon: "" },
    { Title: "", Icon: "" },
    { Title: "", Icon: "" },
    // {
    //   CId: "",
    //   Title: "",
    //   Icon: "",
    // },
  ]);

  const [currencies, setCurrencies] = useState([
    // { id: "", Symbol: "", price: "", Icon: "" },
    { Symbol: "", price: "", Icon: "" },
    { Symbol: "", price: "", Icon: "" },
    { Symbol: "", price: "", Icon: "" },
    { Symbol: "", price: "", Icon: "" },
    { Symbol: "", price: "", Icon: "" },
    { Symbol: "", price: "", Icon: "" },
  ]);
  const [cryptos, setCryptos] = useState([
    // { id: "", Symbol: "", price: "", Icon: "" },
    { Symbol: "", price: "", Icon: "" },
    { Symbol: "", price: "", Icon: "" },
    { Symbol: "", price: "", Icon: "" },
    { Symbol: "", price: "", Icon: "" },
    { Symbol: "", price: "", Icon: "" },
    { Symbol: "", price: "", Icon: "" },
  ]);
  const [blogposts, setBlogposts] = useState([
    { FilePath: "", Titr: "", Lead: "", NewID: "" },
    { FilePath: "", Titr: "", Lead: "", NewID: "" },
    { FilePath: "", Titr: "", Lead: "", NewID: "" },
    { FilePath: "", Titr: "", Lead: "", NewID: "" },
  ]);
  const [paidbiz, setPaidbiz] = useState([
    {
      Title: "",
      CatTitle: "",
      Location: "",
      Rate: "",
      PhotoPath: "",
      Url: "",
    },
    {
      Title: "",
      CatTitle: "",
      Location: "",
      Rate: "",
      PhotoPath: "",
      Url: "",
    },
    {
      Title: "",
      CatTitle: "",
      Location: "",
      Rate: "",
      PhotoPath: "",
      Url: "",
    },
    // FilePath: "", Titr: "", Lead: "", NewID: ""
  ]);

  // this 2 satae for Meta Tags
  const [MTagsCome, setMTagsCome] = useState(false);
  const [MetaData, setMetaData] = React.useState({});

  //api header
  const header = {
    ApiKey: "87e4d3a91a554a2196ae8bdf0bfdcdc9",
    LangId: "1",
  };
  useEffect(() => {
    // Meta Tags Api
    axios
      .get(domain + "/api/meta-index", { headers: header })
      .then((response) => {
        console.log("metaindex");
        console.log(response.data);
        setMTagsCome(true);
        setMetaData(response.data);
      })
      .catch((error) => {
        // alert(error);
      });

    axios
      .get(domain + "/api/nearbybiz", { headers: header })
      .then((response) => {
        if ((response.data.msg = "done")) {
          setNearbybiz(response.data.BizList);
        }
      })
      .catch((error) => {
        // alert(error);
      });

    axios
      .get(domain + "/api/latestbiz", { headers: header })
      .then((response) => {
        setLatestbiz(response.data);
      })
      .catch((error) => {
        // alert(error);
      });

    axios
      .get(domain + "/api/currencies", { headers: header })
      .then((response) => {
        setCurrencies(response.data);
      })
      .catch((error) => {
        // alert(error);
      });

    axios
      .get(domain + "/api/cryptos", { headers: header })
      .then((response) => {
        setCryptos(response.data);
      })
      .catch((error) => {
        // alert(error);
      });

    axios
      .get(domain + "/api/bizrandomcat", { headers: header })
      .then((response) => {
        if ((response.msg = "done")) {
          setBizrandomcat(response.data.BizList);
          setBizrandomTit(response.data.Category);
        }
      })
      .catch((error) => {
        // alert(error);
      });

    axios
      .get(domain + "/api/categories", { headers: header })
      .then((response) => {
        // setCategories([{}]);

        setCategories(response.data);
        console.log(categories);
      })
      .catch((error) => {
        // alert(error);
      });

    axios
      .get(domain + "/api/blogposts", { headers: header })
      .then((response) => {
        setBlogposts(response.data);
      })
      .catch((error) => {
        // alert(error);
      });

    axios
      .get(domain + "/api/paidbiz", { headers: header })
      .then((response) => {
        setPaidbiz(response.data);
      })
      .catch((error) => {
        // alert(error);
      });
  }, []);

  return (
    <>
      {MTagsCome ? <MetaTagsSeo data={MetaData} /> : ""}

      <div className="container">
        {/* کارت های کسب‌وکارهای اطراف */}
        <div className="mb-65 mb-xs-31">
          <div className="d-flex align-items-end justify-content-between titbox-index-res mb-md-3">
            <div className="d-flex align-items-end ">
              <img className="tit-img1 tit-img" src={kasbokar} alt="..." />
              {/* <Link to="/result" className="color-tit"> */}
              <p className="color-tit2 color-tit my-0 fw-800 fs-18 fs-xs-14 color-101010 ">
                کسب‌وکارهای اطراف
              </p>
              {/* </Link> */}
            </div>
            {/* <Link to="/result" className="hover-5ae6a2">
              <p className="my-0 fs-14 fs-xs-14 color-7e7e7e hover-5ae6a2 color-sm-101010">
                بیشتر
              </p>
            </Link> */}
          </div>

          <div className="index-rec-cards">
            {nearbybiz.map((item) => (
              <div
                className="col-12 col-md-4 rec-md rec-center mt-sm-0"
                key={item.BId}
              >
                <RecCard
                  title={item.Title}
                  desc1={item.CatTitle}
                  desc2={item.Location}
                  rate={item.Rate}
                  img={item.PhotoPath}
                  url={item.Url}
                />
              </div>
            ))}
          </div>
        </div>
      </div>

      {/* <div className="index-rec-cards index-rec-cards2">
            {nearbybiz.map((item) => (
              <div className="col-12 col-md-4 rec-md rec-center mt-sm-0" key={item.BId}>
                <RecCard
                  title={item.Title}
                  desc1={item.CatTitle}
                  desc2={item.Location}
                  rate={item.Rate}
                  img={item.PhotoPath}
                  url={item.Url}
                />
              </div>
            ))}
          </div> */}

      {/* اسلایدر کارت تازه‌ترین کسب‌وکارها */}
      <div className="container ps-sm-0 p-xs-0">
        <div className="mb-40 mb-xs-12">
          <div className="d-flex align-items-end justify-content-between titbox-index-res mb-0 px-2 px-md-0">
            <div className="d-flex align-items-end ">
              <img className="tit-img" src={newkasbokar} alt="..." />
              {/* <Link to="/result" className="color-tit"> */}
              <p className="color-tit2 my-0 fw-800 fs-18 fs-xs-14 color-101010 color-tit">
                تازه‌ترین کسب‌وکارها
              </p>
              {/* </Link> */}
            </div>
          </div>
          {/* <CardCarousel withRate={true} /> */}
          <div className="d-none d-md-block">
            <CardCarousel data={latestbiz} withRate={true} />
          </div>
          <div className="d-block d-md-none">
            <ScrollCategory
              data={latestbiz}
              isCarousel={true}
              withRate={true}
            />
          </div>
        </div>
      </div>

      <div className="container">
        {/*معرفی به گیشنیز*/}
        <div className="text-align-centerr d-none d-md-block">
          <div className="moarefi d-flex justify-content-around  align-items-center ">
            <div className=" fs-17 fw-bold align-self-center text-center ">
              کسب‌وکار فارسی‌زبان اطرافت می‌شناسی؟
              <br />
              به گیشنیز معرفی کن
            </div>
            <img
              className="moarefi-icon"
              src={presentation}
              alt="moarefi icon"
            />
            <Link
              className="moarefi-btn align-self-center shadow-off fs-18 color-242424"
              to="/introduce-business"
            >
              برای معرفی کلیک کن{" "}
            </Link>
          </div>
        </div>
        {/* بلاگ گیشنیز */}
        <div className="mb-65 mb-xs-16 mb-sm-3 ">
          <div className="d-flex align-items-end justify-content-between titbox-index-res mb-md-3">
            <div className="d-flex align-items-end ">
              <img className="tit-img" src={blog} alt="..." />
              <a
                className="color-tit"
                href="https://blog.gishniz.com/"
                target="_blank"
              >
                <p className="my-0 fw-800 fs-18 fs-xs-14 color-101010 color-tit">
                  بلاگ گیشنیز
                </p>
              </a>
            </div>
          </div>
          <div className="row mb-md-5">
            <div className="col-12 col-xl-7 col-md-6 mb-4 mb-md-0  px-md-2 px-auto px-0 px-sm-12 ">
              <OverlayCard
                img={blogposts[0].FilePath}
                title={blogposts[0].Titr}
                desc={blogposts[0].Lead}
                url={blogposts[0].PageURL}
              />
            </div>
            <div className="col-12 col-xl-5 col-md-6 pe-md-2 px-auto  ">
              <div className="mb-10px mb-xs-14 rec-center">
                <Rec2Card
                  id={blogposts[1].NewID}
                  img={blogposts[1].FilePath}
                  title={blogposts[1].Titr}
                  desc={blogposts[1].Lead}
                  url={blogposts[1].PageURL}
                />
              </div>
              <div className="mb-10px mb-xs-14 rec-center">
                <Rec2Card
                  id={blogposts[2].NewID}
                  img={blogposts[2].FilePath}
                  title={blogposts[2].Titr}
                  desc={blogposts[2].Lead}
                  url={blogposts[2].PageURL}
                />
              </div>
              <div className=" rec-center">
                <Rec2Card
                  id={blogposts[3].NewID}
                  img={blogposts[3].FilePath}
                  title={blogposts[3].Titr}
                  desc={blogposts[3].Lead}
                  url={blogposts[3].PageURL}
                />
              </div>
            </div>
          </div>
        </div>

        {/* معرفی به گیشنیز سایز موبایل*/}
        <div className="text-align-centerr d-block d-md-none">
          <div className="moarefi d-flex justify-content-between  align-items-center ">
            <div className=" align-self-center text-end text-white alert-m-text">
            کسب‌وکار فارسی‌زبان اطرافت می‌شناسی؟
              <br />
              به گیشنیز معرفی کن
           
            </div>

            <Link
              className="align-self-center color-242424 alert-m-btn hov-shadow-btn shadow-off"
              to="/introduce-business"
            >
              برای معرفی کلیک کن{" "}
            </Link>
          </div>
        </div>
      </div>

      {/* قیمت ارز */}
      <div className="mb-65 mb-xs-20">
        <div className="container">
          <div className="row">
            <div className="col-12 col-md-6 ps-md-2 mb-xs-16">
              <div className="d-flex align-items-end justify-content-between titbox-index-res mb-sm-3 mb-xs-10">
                <div className="d-flex align-items-end ">
                  <img className="tit-img" src={arz} alt="..." />
                  {/* <a className="color-tit" href="#!"> */}
                  <p className="my-0 fw-800 fs-18 fs-xs-14 color-101010 color-tit">
                    قیمت ارز
                  </p>
                  {/* </a> */}
                </div>
              </div>
              <div className="rec-center">
                {currencies.map((item) => (
                  <CurrencyCard
                    currencies={true}
                    key={item.id}
                    // isRed={false}
                    title={item.Symbol}
                    price={item.price}
                    icon={item.Icon}
                  />
                ))}
                {/* <CurrencyCard
                  isRed={true}
                  title={"دلار کانادا"}
                  price={"33.500"}
                /> */}
              </div>
            </div>
            <div className="col-12 col-md-6 pe-md-2">
              <div className="d-flex align-items-end justify-content-between titbox-index-res mb-sm-3 mt-md-0 mt-sm-3 mb-xs-10">
                <div className="d-flex align-items-end ">
                  <img className="tit-img" src={arzDigital} alt="..." />
                  {/* <a className="color-tit" href="#!"> */}
                  <p className="my-0 fw-800 fs-18 fs-xs-14 color-101010 color-tit">
                    قیمت ارز دیجیتال
                  </p>
                  {/* </a> */}
                </div>
              </div>
              <div className="rec-center">
                {cryptos.map((item) => (
                  <CurrencyCard
                    currencies={false}
                    key={item.id}
                    // isRed={true}
                    title={item.Symbol}
                    price={item.price}
                    icon={item.Icon}
                  />
                ))}
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="container">
        {/* کارت های تبلیغات */}
        <div className="mb-65 mb-xs-28">
          <div className="d-flex align-items-end justify-content-between titbox-index-res mb-xs-10 mb-md-3">
            <div className="d-flex align-items-end ">
              <img className="tit-img" src={tabliq} alt="..." />
              {/* <Link to="/result" className="color-tit"> */}
              <p className="my-0 fw-800 fs-18 fs-xs-14 color-101010 color-tit">
                تبلیغات
              </p>
              {/* </Link> */}
            </div>
          </div>
          <div className="index-rec-cards">
            {paidbiz.map((item) => (
              <div className="col-12 col-md-4 rec-md rec-center" key={item.BId}>
                <RecCard
                  title={item.Title}
                  desc1={item.CatTitle}
                  desc2={item.Location}
                  rate={item.Rate}
                  img={item.PhotoPath}
                  url={item.Url}
                />
              </div>
            ))}
          </div>
        </div>
      </div>

      {/* اسلایدر کارت خرید */}
      <div className="container ps-sm-0 p-xs-0">
        <div className="mb-65 mb-xs-20">
          <div className="d-flex align-items-end justify-content-between titbox-index-res mb-0 px-2">
            <div className="d-flex align-items-end ">
              <img className="tit-img" src={bizrandomTit.Icon} alt="..." />
              {/* <Link to="/result" className="color-tit"> */}
              <p className="my-0 fw-800 fs-18 fs-xs-14 color-101010 color-tit">
                {bizrandomTit.Title}
              </p>
              {/* </Link> */}
            </div>
          </div>
          <div className="d-none d-md-block">
            <CardCarousel data={bizrandomcat} withRate={true} />
          </div>
          <div className="d-block d-md-none">
            <ScrollCategory
              data={bizrandomcat}
              isCarousel={true}
              withRate={true}
            />
          </div>
        </div>
      </div>

      {/*دسته‌بندی‌ */}
      <div className="container p-xs-0">
        <div className="mb-65 mb-xs-60">
          <ScrollCategory
            data2={categories}
            isCarousel={false}
            withRate={true}
          />
        </div>
      </div>

      {/* دانلود اپلیکیشن گیشنیز*/}
      <div className="container">
        <div className="mb-45 mb-xs-40">
          <Application />
        </div>
      </div>
    </>
  );
};

export default HomeBody;
