import React, { useState, useEffect, useContext } from "react";
import { MDBCol, MDBRow, MDBInput } from "mdb-react-ui-kit";
import axios from "axios";
import { DomainContext } from "../../../App";
// rtl
import { createTheme, ThemeProvider } from "@mui/material/styles";
import TextField from "@mui/material/TextField";
import rtlPlugin from "stylis-plugin-rtl";
import { prefixer } from "stylis";
import { CacheProvider } from "@emotion/react";
import createCache from "@emotion/cache";
//components
import Sidbar from "../../../components/Dashbord/sidbar/Sidbar";
import Main from "../../../components/Dashbord/Main/Main";
import "../indexprofile.css";
import Header_mobile from "../../../components/Dashbord/Header_mobile/Header_mobile";
import AlertSuccess from "../../../components/Dashbord/AlertSuccess/AlertSuccess";

const theme = createTheme({
  direction: "rtl", // Both here and <body dir="rtl">
});
// Create rtl cache
const cacheRtl = createCache({
  key: "muirtl",
  stylisPlugins: [prefixer, rtlPlugin],
});

const Changepass = (props) => {
  // for domain of Apis
  const domain = useContext(DomainContext);

  const [disable, setDisable] = useState(false);
  const [apiErr, setApiErr] = useState("");
  const [passErr, setPassErr] = useState(false);

  const [data, setData] = useState({
    MId: "",
    OldPassword: "",
    NewPassword: "",
    RepeatNewPassword: "",
  });

  const inputChangeHandler = (event) => {
    setPassErr(false);
    setApiErr("");
    setData({
      ...data,
      [event.target.name]: event.target.value,
    });
  };

  const [showAlert, setshowAlert] = useState(false);
  const alertHandeler = () => {
    setshowAlert(true);
    setTimeout(() => {
      // After 3 seconds set the show value to false
      setshowAlert(false);
    }, 4000);
  };

  function getCookie(cname) {
    let name = cname + "=";
    let decodedCookie = decodeURIComponent(document.cookie);
    let ca = decodedCookie.split(";");
    for (let i = 0; i < ca.length; i++) {
      let c = ca[i];
      while (c.charAt(0) == " ") {
        c = c.substring(1);
      }
      if (c.indexOf(name) == 0) {
        return c.substring(name.length, c.length);
      }
    }
    return "";
  }

  useEffect(() => {
    if (getCookie("MemId")) {
    } else {
      props.history.push({
        pathname: "/login",
      });
    }
  }, []);

  const headerPost = {
    ApiKey: "87e4d3a91a554a2196ae8bdf0bfdcdc9",
  };

  const submitHandler = (event) => {
    event.preventDefault();
    setPassErr(false);
    setApiErr("");

    if (data.NewPassword != data.RepeatNewPassword) {
      setPassErr(true);
    } else {
      setDisable(true);

      const dataPost = {
        MId: getCookie("MemId"),
        OldPassword: data.OldPassword,
        NewPassword: data.NewPassword,
      };
      console.log(dataPost);

      axios
      .post(domain+"/api/member/change-password", dataPost, {
        headers: headerPost,
      })
      .then((response) => {
        setDisable(false);
        console.log(response.data);
        if (response.data.msg == "done") {
          alertHandeler();
        } else if (response.data.msg == "oldpass-invalid") {
          setApiErr("رمز عبور فعلی اشتباه است.");
        }else if (response.data.msg == "error") {
          setApiErr("خطا در ثبت اطلاعات لطفا دوباره امتحان کنید.");
        } else if (response.data.msg == "fill-all") {
          setApiErr("لطفا همه موارد را پر کنید.");
        }
      })
      .catch((error) => {
        // alert(error);
      });
    }
  };

  return (
    <>
      <Header_mobile />
      <div>
        <CacheProvider value={cacheRtl}>
          <ThemeProvider theme={theme}>
            <div className="margin-0 d-flex">
              <div className="border-left-dashbord d-none d-md-block">
                <Sidbar />
              </div>
              <div className="bg-main-dashbord">
                <Main lencomments={2}>
                  <h5 className="header-main">تغییر رمز عبور</h5>
                  <div className="centerr mt-3 mt-md-0">
                    <TextField
                      name="OldPassword"
                      value={data.OldPassword}
                      onChange={inputChangeHandler}
                      id="outlined-basic"
                      label="رمز عبور فعلی"
                      variant="outlined"
                      className={
                        "label-field-less7 custom-text-field custom-text-field-dash mb-0 dash-text-field"
                      }
                    />
                    <div className="mt15">
                      <TextField
                        name="NewPassword"
                        value={data.NewPassword}
                        onChange={inputChangeHandler}
                        id="outlined-basic"
                        label="رمز عبور جدید"
                        variant="outlined"
                        className={
                          " custom-text-field-dash label-field-less7 custom-text-field  mb-0 dash-text-field"
                        }
                      />
                    </div>
                    <div className="mt15">
                      <TextField
                        name="RepeatNewPassword"
                        value={data.RepeatNewPassword}
                        onChange={inputChangeHandler}
                        id="outlined-basic"
                        label="تکرار رمز عبور جدید"
                        variant="outlined"
                        className={
                          "label-field-less7 custom-text-field custom-text-field-dash mb-0 dash-text-field"
                        }
                      />
                    </div>
                    {passErr && (
                      <div className="centerr mt15 input-error-dash text-center">
                        تکرار رمز عبور مطابقت ندارد.
                      </div>
                    )}
                    <div className="centerr mt15 input-error-dash text-center">
                      {apiErr}
                    </div>
                    <div className="row_saveButton">
                      <button
                        disabled={disable}
                        onClick={submitHandler}
                        className="saveButton"
                      >
                        تغییر رمز
                      </button>
                    </div>
                  </div>
                </Main>
              </div>
            </div>
          </ThemeProvider>
        </CacheProvider>
      </div>
      {/* alert delete */}
      <AlertSuccess
        showAlert={showAlert}
        text={" اطلاعات شما با موفقیت ثبت شد."}
      />
    </>
  );
};

export default Changepass;
