import React from "react";
import Header2 from "../../components/header/Header2";
import Headermobile from "../../components/header-mobile/Headermobile";
import Footer from "../../components/footer/Footer";
import "./gish-pages.css";
const AboutUs = () => {
  return (
    <>
      <div className="d-none d-md-block">
        <Header2 />
      </div>
      <div className="d-block d-md-none">
        <div className="fixed-header">
          <Headermobile />
        </div>
        <div className="box-under-header-res"></div>
      </div>
      <div className="gish-page-back">
        <div className="gish-about-tit">
          درباره <span className="color-5ae6a2">ما</span>
        </div>
        <div className="gish-about-body">
          گیشنیز در بهار ۱۴۰۱ سبز شد تا به فارسی‌زبانان جهان کمک کنه هرجای دنیا
          که هستن کسب‌وکارهای هم‌زبون و آشنا رو پیدا کنن و زندگی روزمره براشون
          راحت‌تر بشه. دیگه فرقی نمیکنه که از ایران به کمک یک وکیل در کانادا
          نیاز داری یا تو قلب آمریکا دنبال آدرس یک کبابی خوب نزدیک محل زندگیت
          می‌گردی. فرقی نمی‌کنه توی اروپا زندگی می‌کنی و ترجیح می‌دی پزشک
          هم‌زبون معاینه‌ت کنه یا از هر قاره‌ای که هستی دلتنگِ یک نشونه‌ از
          کشورتی؛ گیشنیز اینجاست که دستت رو بگیره و به کسی که لازم داری وصلت
          کنه. گیشنیز یک بانک کم‌نظیر اطلاعاتی از کسب‌و‌کارهای فارسی‌زبانِ سراسر
          دنیاست که این امکان رو بهت می‌ده تا با حفظ حریم خصوصی و تنها با
          جست‌وجوی یک کلیدواژه، اطلاعات مورد نظرت رو به‌روز، سریع و دقیق پیدا
          کنی.
          <br />
          <br className="d-md-none" />
           اگر هم صاحب کسب‌وکاری و دوست داری هم‌زبون‌هات بشناسنت، توی گیشنیز
          می‌تونی کار و خدماتت رو معرفی کنی و صفحه مخصوص به خودت رو با جزییاتی
          که می‌خوای داشته باشی.
        </div>
        <div className="gish-about-end">
        به گیشنیز خوش‌ اومدی!
        </div>
      </div>
      <Footer />
    </>
  );
};

export default AboutUs;
