import React, { useState, useEffect, useContext } from "react";
import axios from "axios";
import { styled } from "@mui/material/styles";
import ArrowForwardIosSharpIcon from "@mui/icons-material/ArrowForwardIosSharp";
import MuiAccordion from "@mui/material/Accordion";
import MuiAccordionSummary from "@mui/material/AccordionSummary";
import MuiAccordionDetails from "@mui/material/AccordionDetails";
import Typography from "@mui/material/Typography";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import TextField from "@mui/material/TextField";
import rtlPlugin from "stylis-plugin-rtl";
import { prefixer } from "stylis";
import { CacheProvider } from "@emotion/react";
import createCache from "@emotion/cache";
import "./RTLAccordian.css";

import { DomainContext } from "../../App";

const theme = createTheme({
  direction: "rtl", // Both here and <body dir="rtl">
});
// Create rtl cache
const cacheRtl = createCache({
  key: "muirtl",
  stylisPlugins: [prefixer, rtlPlugin],
});
// styles
const Accordion = styled((props) => (
  <MuiAccordion disableGutters elevation={0} square {...props} />
))(({ theme }) => ({
  borderRadius: `10px`,
  borderBottom: `1px solid white`,
  "&:not(:last-child)": {
    borderBottom: 0,
  },
  "&:before": {
    display: "none",
  },
}));
const AccordionSummary = styled((props) => (
  <MuiAccordionSummary
    expandIcon={<ArrowForwardIosSharpIcon sx={{ fontSize: "0.9rem" }} />}
    {...props}
  />
))(({ theme }) => ({
  minHeight: "44px",
  height: "44px",
  padding: "0 23px 0 16px",
  borderTop: "1px solid #8ef0c0",
  backgroundColor: "#fff",
  flexDirection: "row",
  "& .MuiAccordionSummary-content": {
    marginLeft: theme.spacing(1),
  },
}));

const AccordionDetails = styled(MuiAccordionDetails)(({ theme }) => ({
  padding: theme.spacing(2),
  borderTop: "1px solid #8ef0c0",
  margin: "0 29px",
  padding: "10px 0",
}));

//   component
const RTLAccordian = (props) => {
  // for domain of Apis
  const domain = useContext(DomainContext);

  const [expanded, setExpanded] = React.useState("");

  const handleChange = (panel, open) => (event, newExpanded) => {
    if (open) {
      setExpanded(newExpanded ? panel : false);
    }
  };
  const [category, setCategory] = useState([]);
  const [option, setOption] = useState([]);
  const [peyment, setpeyment] = useState([]);
  //api header
  const header = {
    ApiKey: "87e4d3a91a554a2196ae8bdf0bfdcdc9",
    LangId: "1",
  };

  useEffect(() => {
    axios
      .get(domain + "/api/category-parent", { headers: header })
      .then((response) => {
        // setLatestbiz(response.data);
        if (response.data.msg == "done") {
          setCategory(response.data.CatList);
        }
      })
      .catch((error) => {
        // alert(error);
      });
    axios
      .get(domain + "/api/category-options", {
        headers: header,
      })
      .then((response) => {
        // setLatestbiz(response.data);
        if (response.data.msg == "done") {
          setOption(response.data.OptionsList);
        }
      })
      .catch((error) => {
        // alert(error);
      });

    axios
      .get(domain + "/api/pyments-type", { headers: header })
      .then((response) => {
        // setLatestbiz(response.data);
        if (response.data.msg == "done") {
          setpeyment(response.data.PayList);
        }
      })
      .catch((error) => {
        // alert(error);
      });
  }, []);

  return (
    <div>
      <CacheProvider value={cacheRtl}>
        <ThemeProvider theme={theme}>
          <div>
            <Accordion
              expanded={expanded === "panel1"}
              onChange={handleChange("panel1", option.length > 0)}
            >
              <AccordionSummary
                aria-controls="panel1d-rtl-content"
                id="panel1d-rtl-header"
              >
                <Typography>امکانات</Typography>
              </AccordionSummary>
              <AccordionDetails>
                <Typography>
                  {/* {emkanat.map((item) => (
                    <div key={item.id} className="result-filter-item">
                      <label className="container-s2 m-0 me-0 mb-0 d-inline-flex">
                        <input type="checkbox" />
                        <span className="checkmark-s2 back-white"></span>
                        <span>{item.title}</span>
                      </label>
                    </div>
                  ))} */}
                  {option.length ? (
                    option.map((item) => (
                      <div key={item.COTId} className="result-filter-item">
                        <label className="container-s2 m-0 me-0 mb-0 d-inline-flex">
                          <input type="checkbox" />
                          <span className="checkmark-s2 back-white"></span>
                          <span>{item.Title}</span>
                        </label>
                      </div>
                    ))
                  ) : (
                    <p></p>
                  )}
                </Typography>
              </AccordionDetails>
            </Accordion>
            <Accordion
              expanded={expanded === "panel2"}
              onChange={handleChange("panel2", peyment.length > 0)}
            >
              <AccordionSummary
                aria-controls="panel2d-rtl-content"
                id="panel2d-rtl-header"
              >
                <Typography>روش پرداخت</Typography>
              </AccordionSummary>
              <AccordionDetails>
                <Typography>
                  {peyment.length ? (
                    peyment.map((item) => (
                      <div key={item.PTId} className="result-filter-item">
                        <label className="container-s2 m-0 me-0 mb-0 d-inline-flex">
                          <input type="checkbox" />
                          <span className="checkmark-s2 back-white"></span>
                          <span>{item.Title}</span>
                        </label>
                      </div>
                    ))
                  ) : (
                    <p></p>
                  )}
                </Typography>
              </AccordionDetails>
            </Accordion>
            <Accordion
              expanded={expanded === "panel3"}
              onChange={handleChange("panel3", category.length > 0)}
            >
              <AccordionSummary
                aria-controls="panel3d-rtl-content"
                id="panel3d-rtl-header"
              >
                <Typography>دسته‌بندی</Typography>
              </AccordionSummary>
              <AccordionDetails>
                <Typography>
                  {category.length ? (
                    category.map((item) => (
                      <div key={item.CId} className="result-filter-item">
                        <label className="container-s2 m-0 me-0 mb-0 d-inline-flex">
                          <input type="checkbox" />
                          <span className="checkmark-s2 back-white"></span>
                          <span>{item.Title}</span>
                        </label>
                      </div>
                    ))
                  ) : (
                    <p></p>
                  )}
                </Typography>
              </AccordionDetails>
            </Accordion>
          </div>
        </ThemeProvider>
      </CacheProvider>
    </div>
  );
};

export default RTLAccordian;