import React, { useState, useEffect, useContext } from "react";
import { Link, useHistory } from "react-router-dom";
import { MDBBtn, MDBContainer, MDBRow } from "mdb-react-ui-kit";
import Footer from "../footer/Footer";
import Facebook from "./Facebook";
import Google from "./Google";
import axios from "axios";
import { useLocation } from "react-router-dom";
import jwt_decode from "jwt-decode";
import { validate } from "./validate";
import { DomainContext, CookieContext } from "../../App";

import MetaTagsSeo from "../meta-tags-seo/MetaTagsSeo";

import closeIcon from "../../images/header/close.png";
import logo from "../../images/home-body/logo gishniz.png";
import facebook_login from "../../images/facebook-login.png";
import google_login from "../../images/google-login.png";
import { Password } from "@mui/icons-material";

const Login = (props) => {
  const { state } = props.location;
  // for domain of Apis
  const domain = useContext(DomainContext);
  // cookie context
  const { cookieState, setCookieState } = useContext(CookieContext);

  // this 2 satae for Meta Tags
  const [MTagsCome, setMTagsCome] = useState(false);
  const [MetaData, setMetaData] = React.useState({});

  const [inputData, setInputData] = useState({
    username: "",
    password: "",
  });

  const [errors, setErrors] = useState({});
  const [touched, setTouched] = useState({});

  const [existErr, setExistErr] = useState();
  const [fillErr, setFillErr] = useState();
  const [serverErr, setServerErr] = useState();
  const [disable, setDisable] = useState(false);

  const history = useHistory();

  const location = useLocation();
  const queryParams = new URLSearchParams(location.search);
  const backUrl = queryParams.get("target");
  console.log("backUrl");
  console.log(backUrl);

  useEffect(() => {
    if (getCookie("MemId")) {
      props.history.push({
        pathname: "/members/profile",
      });
    }
  }, []);

  //api header
  const header = {
    ApiKey: "87e4d3a91a554a2196ae8bdf0bfdcdc9",
    Username: inputData.username,
    Password: inputData.password,
  };

  useEffect(() => {
    setErrors(validate(inputData, "login"));
  }, [inputData, touched]);

  // const inputFocusHanlder = (event) => {
  //   setTouched({ ...touched, [event.target.name]: true });
  // };
  const [Errorrr, setErrorrr] = useState(false);
  const [ErrorrrRed, setErrorrrRed] = useState(false);

  const inputChangeHandler = (event) => {
    setErrorrrRed(false);
    setInputData({ ...inputData, [event.target.name]: [event.target.value] });
  };

  const registerHandler = (event) => {
    props.history.push({
      pathname: "/register",
      state: backUrl, // your data array of objects
    });
  };

  const submitHandler = (event) => {
    console.log("11");
    event.preventDefault();
    // setErrorrrRed(false);

    // if (!Object.keys(errors).length) {
    // if (!Errorrr) {
    //   // success
    //   if (state) {
    //     props.history.push({
    //       pathname: "/introduce-business/introduce-business-thanks",
    //       state: state, // your data array of objects
    //     });
    //   }
    // } else {
    //   setErrorrrRed(true);
    // }
    console.log("22");

    // setErrorrrRed(true);
    setErrorrrRed(false);
    console.log("33");

    if (!Object.keys(errors).length) {
      // success
      setExistErr(false);
      setServerErr(false);
      setFillErr(false);
      setDisable(true);
      console.log("44");

      axios
        .get(domain + "/api/member-check-login", {
          headers: header,
        })
        .then((response) => {
          setDisable(false);
          console.log(response);

          if (response.data.msg == "done") {
            setCookie("MemId", response.data.MemberDetail.MemId, 1);
            localStorage.setItem(
              "MemberDetail",
              JSON.stringify(response.data.MemberDetail)
            ); //set local
            setCookieState(response.data.MemberDetail.MemId);

            if (backUrl == null) {
              props.history.push({
                pathname: "/",
                // state: response.data.OwnerDetail.OwnerId, // your data array of objects
              });
            } else {
              props.history.push({
                pathname: backUrl,
              });
            }
          } else if (response.data.msg == "login-failed") {
            console.log("login-failed");
            setExistErr(true);
            setErrorrrRed(true);
          } else if (response.data.msg == "error") {
            console.log("error");
            setServerErr(true);
          } else if (response.data.msg == "fill-all") {
            console.log("fill-all");
            setFillErr(true);
          }
        })
        .catch((error) => {
          // alert(error);
        });
    } else {
      setTouched({
        username: true,
        password: true,
      });
    }
    // // Cookies
    // let userNameCookie = getCookie("userName");
    // let passWordCookie = getCookie("passWord");

    // if (userNameCookie != "" && passWordCookie != "") {
    //   if (
    //     userNameCookie == inputData.username[0] &&
    //     passWordCookie == inputData.password[0]
    //   ) {
    //     alert("Welcome " + userNameCookie);
    //     history.push("/");
    //   } else {
    //     // alert("userName or passWord incorrect.");
    //     setInputData({
    //       username: "",
    //       password: "",
    //     });
    //   }
    // } else {
    //   setCookie("userName", inputData.username[0], 10);
    //   setCookie("passWord", inputData.password[0], 10);
    //   let userNameCookieonline = getCookie("userName");
    //   // alert("Welcome " + userNameCookieonline);
    //   history.push("/");
    // }
  };

  //setCookie
  // cname == Cookiename
  // cvalue == Cookievalue
  // exdays == expierday
  function setCookie(cname, cvalue, exdays) {
    const d = new Date();
    d.setTime(d.getTime() + exdays * 24 * 60 * 60 * 1000);
    let expires = "expires=" + d.toUTCString();
    document.cookie = cname + "=" + cvalue + ";" + expires + ";path=/";
  }
  function getCookie(cname) {
    let name = cname + "=";
    let decodedCookie = decodeURIComponent(document.cookie);
    let ca = decodedCookie.split(";");
    for (let i = 0; i < ca.length; i++) {
      let c = ca[i];
      while (c.charAt(0) == " ") {
        c = c.substring(1);
      }
      if (c.indexOf(name) == 0) {
        return c.substring(name.length, c.length);
      }
    }
    return "";
  }

  //api header
  const headerMeta = {
    ApiKey: "87e4d3a91a554a2196ae8bdf0bfdcdc9",
    Page: "login",
  };

  useEffect(() => {
    axios
      .get(domain + "/api/member/meta-login-register", { headers: headerMeta })
      .then((response) => {
        console.log("response.data");
        console.log(response.data);
        setMTagsCome(true);
        setMetaData(response.data);
      })
      .catch((error) => {
        // alert(error);
      });
  }, []);

  // function handleCallbackResponse(response) {
  //   console.log("encode" + response.credential);
  //   var userObject = jwt_decode(response.credential);
  //   console.log(userObject);
  // }
  // useEffect(() => {
  //   // global google
  //   window.google.accounts.id.initialize({
  //     client_id:
  //       "25572161176-nr7ueb5nla0r4b2j0gf70t7j3n5uffl9.apps.googleusercontent.com",
  //     callback: handleCallbackResponse,
  //   });
  //   window.google.accounts.id.renderButton(
  //     document.getElementById("signInId"),
  //     {
  //       theme: "outlined",
  //       size: "large",
  //     }
  //   );
  // }, []);

  return (
    <>
      {MTagsCome ? <MetaTagsSeo data={MetaData} /> : ""}
      {/* <div id="signInId"></div> */}
      <div className="centerr">
        <div className="centerr d-none d-md-block">
          <Link to="/" className=" logo-login-a ">
            <img src={logo} alt="logo gishniz" className="logo-login-lap" />
          </Link>
        </div>

        <div className="login-container centerr">
          <MDBContainer className="">
            <MDBRow className="text-end">
              <Link to="/" className="a-close-img">
                <img
                  className="login-close-img me-0"
                  src={closeIcon}
                  alt="close icon"
                />
              </Link>
            </MDBRow>
            <MDBRow className="">
              <div className="col-12 text-center">
                <div className="centerr">
                  <Link to="/" className=" logo-login-a d-block d-md-none">
                    <img
                      src={logo}
                      alt="logo gishniz"
                      className="logo-login "
                    />
                  </Link>
                </div>

                <p className=" login-matn1 fs-xss-21 color-515151 fw-bold mt-3 mt-md-0 mb-2">
                  ورود به حساب کاربری
                </p>
                <p className="fs-xss-12 color-8d8c8c mb-4 pb-2 pb-md-0 login-matn2">
                  برای استفاده از امکانات سایت لطفا وارد حساب کاربری شوید.
                </p>
              </div>
              <div className="col-12 mt-1 padding-xss-32">
                <MDBBtn className="shadow-off account-login-btn text-start">
                  ورود با فیسبوک
                  <img
                    className="account-login-img"
                    src={facebook_login}
                    alt=""
                  />
                </MDBBtn>
                {/* <Facebook /> */}
                <MDBBtn className="shadow-off account-login-btn text-start">
                  ورود با گوگل
                  <img
                    className="account-login-img"
                    src={google_login}
                    alt=""
                  />
                </MDBBtn>
                {/* <Google /> */}

                <div className="centerr">
                  <div className="divider-login d-flex align-items-center">
                    <p className="text-center mb-0 divider-login-text fs-xss-12">
                      ورود با نام کاربری
                    </p>
                  </div>
                </div>
                <div className="">
                  <form>
                    <input
                      name="username"
                      value={inputData.username}
                      onChange={inputChangeHandler}
                      // onFocus={inputFocusHanlder}
                      className={
                        (errors.username && touched.username) || ErrorrrRed
                          ? "input-login mb-md-1 border-error"
                          : "input-login mb-md-1"
                      }
                      type="text"
                      placeholder="نام کاربری"
                    />

                    {errors.username && touched.username && (
                      <span className="input-error">{errors.username}</span>
                    )}

                    <input
                      name="password"
                      type="password"
                      value={inputData.password}
                      onChange={inputChangeHandler}
                      // onFocus={inputFocusHanlder}
                      maxlength="15"
                      // minlength={6}
                      className={
                        (errors.password && touched.password) || ErrorrrRed
                          ? "input-login border-error mt-2"
                          : "input-login mt-2"
                      }
                      placeholder="رمز عبور"
                    />
                    {errors.password && touched.password && (
                      <span className="input-error">{errors.password}</span>
                    )}
                    {ErrorrrRed && (
                      <span className="input-error">
                        نام کاربری یا رمز عبور اشتباه است.
                      </span>
                    )}
                    {/* {existErr && (
                      <span className="input-error">
                        آدرس ایمیل یا رمز عبور اشتباه است.
                      </span>
                    )} */}
                    {serverErr && (
                      <span className="input-error">
                        خطا در ارتباط با سرور، لطفا دقایقی دیگر مجددا تلاش
                        نمایید.
                      </span>
                    )}
                    {fillErr && (
                      <span className="input-error">
                        لطفا همه موارد را پر کنید.
                      </span>
                    )}

                    <div className="rightt login-matn3-div">
                      <Link
                        className="color-5ae6a2 text-end login-matn3 mb-0"
                        to="/forget-password"
                      >
                        {/* <p className="text-end login-matn3 mb-0 "> */}
                        رمز عبور رو فراموش کردی؟
                        {/* </p> */}
                      </Link>
                    </div>

                    <MDBBtn
                      disabled={disable}
                      onClick={submitHandler}
                      type="submit"
                      className="shadow-off hov-shadow-btn submit-login-btn text-center"
                    >
                      ورود به حساب
                    </MDBBtn>
                    <div className="d-flex justify-content-center mb-4 fs-14 fs-xss-14">
                      <p className="color-242b2e">حساب کاربری ندارید؟ </p>
                      <Link className="color-5ae6a2" onClick={registerHandler}>
                        <p className="text-end me-2">ثبت نام </p>
                      </Link>
                    </div>
                  </form>
                </div>
              </div>
            </MDBRow>
          </MDBContainer>
        </div>
      </div>
      <Footer />
    </>
  );
};

export default Login;
