import React, { useEffect, useState, useContext } from "react";
import axios from "axios";
import MetaTags from "react-meta-tags";

import Header from "../components/header/Header";
import Headermobile from "../components/header-mobile/Headermobile";
import HomeBody from "../components/home-body/HomeBody";
import Footer from "../components/footer/Footer";
import Listmobile from "../components/list-mobile/Listmobile";
import MoarefiAlert from "../components/moarefi-alert/MoarefiAlert";
import { Title } from "@mui/icons-material";
import MetaTagsSeo from "../components/meta-tags-seo/MetaTagsSeo";

import { DomainContext } from "../App";

const HomePage = () => {
  // for domain of Apis
  const domain = useContext(DomainContext);

  const [dataCome, setDataCome] = useState(false);

  // this 2 satae for Meta Tags
  const [MTagsCome, setMTagsCome] = useState(false);
  const [MetaData, setMetaData] = React.useState({});

  const [data, setData] = useState({
    HeaderLogo: {},
    CategoriesForSearch: [
      {
        CId: "",
        Title: "",
        ShortTitle: "",
        Icon: "",
      },
    ],
    CoverPhoto: "",
    CategoriesOnHeader: [{}, {}, {}, {}],
    CategoriesOnHeaderShort: [{}, {}, {}, {}, {}, {}, {}],
  });

  //api header
  const header = {
    ApiKey: "87e4d3a91a554a2196ae8bdf0bfdcdc9",
    LangId: "1",
  };

  useEffect(() => {
    setDataCome(false);
    // Meta Tags Api
    // axios
    //   .get(domain + "/api/meta-index", { headers: header })
    //   .then((response) => {
    //     console.log("metaindex");
    //     console.log(response.data);
    //     setMTagsCome(true);
    //     setMetaData(response.data);
    //   })
    //   .catch((error) => {
    //     // alert(error);
    //   });
    axios
      .get(domain + "/api/header", { headers: header })
      .then((response) => {
        setData(response.data);
        setDataCome(true);
      })
      .catch((error) => {
        // alert(error);
      });
  }, []);

  return (
    <>
      {/* {MTagsCome ? <MetaTagsSeo data={MetaData} /> : ""} */}
      <div className="d-none d-md-block">
        <Header data={data} dataCome={dataCome} />
      </div>
      <div className="d-block d-md-none">
        <div className="fixed-header">
          <Headermobile data={data} />
        </div>
        <div className="box-under-header-res"></div>

        <Listmobile data={data.CategoriesOnHeaderShort} dataCome={dataCome} />
        <div className="centerr">
          <a
            href="https://biz.gishniz.com/choose-owner"
            target="_blank"
            className="sabt-btn-a  mb-sabt-mobile sabt-mobile-btn hov-shadow-btn-dark shadow-off"
          >
            ثبت کسب‌وکار
          </a>
        </div>
        <MoarefiAlert />
      </div>
      <HomeBody />
      <Footer />
    </>
  );
};

export default HomePage;
