import React, { useState, useEffect, useRef, useContext } from "react";
import { MDBRadio, MDBBtnGroup, MDBBtn } from "mdb-react-ui-kit";
// import { Link } from "react-router-dom";
import TextField from "@mui/material/TextField";
import Autocomplete from "@mui/material/Autocomplete";
import { DomainContext } from "../App";
import axios from "axios";
import Skeleton from "@mui/material/Skeleton";
import { Chip } from "@mui/material";

import { validate } from "../components/login/validate";
import Header2 from "../components/header/Header2";
import Headermobile from "../components/header-mobile/Headermobile";
import Footer from "../components/footer/Footer";
import ButtonGroupBusinness from "../components/ButtonGroupBusinness/ButtonGroupBusinness";
import ButtonGroupBusinness2 from "../components/ButtonGroupBusinness/ButtonGroupBusinness2";

import add1 from "../images/bg-green-add.png";
import iconn from "../images/next-left.png";
import close from "../images/header/close.png";

const IntroduceBusiness = (props) => {
  // for domain of Apis
  const domain = useContext(DomainContext);

  // backend datas
  const [categories, setCategories] = useState([]);
  const [value, setValue] = useState(null);
  const [inputValue, setInputValue] = useState("");

  //
  const [emptyInput, setemptyInput] = useState(true);
  const [CatIdsApi, setCatIdsApi] = useState(null);
  const [childCat, setChildCat] = useState([]);
  const [items, setItems] = useState([]);
  const [changedCid, setChangedCid] = useState(true);
  const [cid, setcid] = useState([]);
  const [locakChild, setlocakChild] = useState(false);
  const [dataCome2, setDataCome2] = useState(false);

  // group validate
  const [groupValidate, setGroupValidate] = useState(true);
  const [showGV, setShowGV] = useState(false);
  const [buttonGroupsId, setbuttonGroupsId] = useState([]);

  // const [inputValues, setInputValues] = useState({});
  const [counter, setCounter] = useState(1); //for input adder
  const [showCounter, setShowCounter] = useState(true); //for input adder

  const [showBtns, setShowBtns] = useState(false);
  const [other, setOther] = useState(false);
  const [otherName, setOtherName] = useState("");
  const inputengName = useRef(null);
  const inputcountryy = useRef(null);
  const inputprovincee = useRef(null);
  const inputcityy = useRef(null);

  const [data, setData] = useState({
    engName: "",
    perName: "",
    countryy: "",
    provincee: "",
    cityy: "",
    addresss: "",
    webOrInsta: {},
    category: "",
    group: "",
    othergroup: "",
  });

  const [countries, setCountries] = useState([]);
  const [provinces, setProvinces] = useState([]);
  const [cities, setCities] = useState([]);

  const [valueCountry, setValueCountry] = useState(null);
  const [inputValueCountry, setInputValueCountry] = useState("");
  const [valueProvince, setValueProvince] = useState(null);
  const [inputValueProvince, setInputValueProvince] = useState("");
  const [valueCity, setValueCity] = useState(null);
  const [inputValueCity, setInputValueCity] = useState("");

  const [CountryId, setCountryId] = useState("");
  const [ProvinceId, setProvinceId] = useState("");

  const [inputDisable, setInputDisable] = useState(true);

  // this 2 are for input adder
  const handleClick = () => {
    if (counter < 4) {
      setCounter(counter + 1);
    }
    if (counter > 2) {
      setShowCounter(false);
    }
  };
  const handleOnChange = (e) => {
    const abc = {};
    abc[e.target.id] = e.target.value;
    setData({ ...data, webOrInsta: { ...data.webOrInsta, ...abc } });
  };

  // this 3 are for button group
  const showOther = (event) => {
    setOther(true);
    setData({ ...data, group: event.target.value });
  };
  const otherNameHandler = (event) => {
    setOtherName(event.target.value);
    setData({ ...data, othergroup: event.target.value });
  };
  const hideOther = (event) => {
    setOther(false);
    setData({ ...data, group: event.target.value, othergroup: "" });
    // make otherName input null
    setOtherName("");
  };

  // for simple inputs
  const simpleInputHandler = (event) => {
    // const result = event.target.value.replace(/[^A-Za-z\s]/gi, '');
    setData({ ...data, [event.target.name]: event.target.value });
    // console.log(data);
  };

  const [errors, setErrors] = useState({});
  const [touched, setTouched] = useState({});

  useEffect(() => {
    setErrors(validate(data, "introduce-business"));
  }, [data, touched]);

  const onButtonClick_fcous = () => {
    console.log(errors);
    let result = Object.keys(errors)[0];
    switch (result) {
      case "engName":
        inputengName.current.focus();
        break;
      case "countryy":
        inputcountryy.current.scrollIntoView();
        break;
      case "provincee":
        inputprovincee.current.scrollIntoView();
        break;
      case "cityy":
        inputcityy.current.scrollIntoView();
        break;
    }
  };

  const submitHandler = (event) => {
    event.preventDefault();
    var size = Object.keys(errors).length;
    if (size > 0) {
      onButtonClick_fcous();
    }

    if (!Object.keys(errors).length) {
      // success
      props.history.push({
        pathname: "/introduce-business/introduce-business-confirm",
        state: data, // your data array of objects
      });
    } else {
      setTouched({
        engName: true,
        countryy: true,
        provincee: true,
        cityy: true,
      });
    }
  };
  // for new autooooooooooooo
  const [list3, setList3] = useState([]);
  const [listCounter, setListCounter] = useState(0);
  const listHandler = (newValue) => {
    if (list3.length < 4) {
      if (!list3.includes(newValue.Title)) {
        setList3([...list3, newValue.Title]);
      }
    }
  };

  const deleteHandler = (num) => {
    if (list3.length > 0) {
      setList3(list3.filter((item) => item !== num));
    }
    setValue(null);
    setData({ ...data, category: "" });
  };

  const header = {
    ApiKey: "87e4d3a91a554a2196ae8bdf0bfdcdc9",
    LangId: "1",
    CountryId: CountryId,
    ProvinceId: ProvinceId,
  };

  const headerCat = {
    ApiKey: "87e4d3a91a554a2196ae8bdf0bfdcdc9",
    LangId: "1",
    Parentid: cid,
  };

  useEffect(() => {
    axios
      .get(domain + "/api/country ", {
        headers: headerCat,
      })
      .then((response) => {
        setCountries(response.data);
      })
      .catch((error) => {
        // alert(error);
      });

    axios
      .get(domain + "/api/parentcate", {
        headers: header,
      })
      .then((response) => {
        if (response.data.msg == "done") {
          console.log("response.data parent");
          console.log(response.data);
          setCategories(response.data.CatList);
        }
      })
      .catch((error) => {
        // alert(error);
      });
  }, []);

  useEffect(() => {
    if (inputValueCountry != "") {
      console.log("provin");

      axios
        .get(domain + "/api/province ", { headers: header })
        .then((response) => {
          setProvinces(response.data);
        })
        .catch((error) => {
          // alert(error);
        });
    }
  }, [CountryId]);

  useEffect(() => {
    if (
      inputValueProvince != "" &&
      inputValueCountry != "" &&
      valueProvince != null
    ) {
      axios
        .get(domain + "/api/city ", { headers: header })
        .then((response) => {
          setCities(response.data);
        })
        .catch((error) => {
          // alert(error);
        });
    }
  }, [ProvinceId]);

  useEffect(() => {
    if (locakChild) {
      setDataCome2(false);
      axios
        .get(domain + "/api/childcate ", { headers: headerCat })
        .then((response) => {
          if (response.data.msg == "done") {
            setChildCat(response.data.CatList);
            setDataCome2(true);
          }
        })
        .catch((error) => {
          // alert(error);
        });
      setlocakChild(false);
    }
  }, [cid, changedCid]);

  return (
    <>
      <div className="d-none d-md-block">
        <Header2 />
      </div>
      <div className="d-block d-md-none">
        <div className="fixed-header">
          <Headermobile data={data} />
        </div>
        <div className="box-under-header-res"></div>
      </div>

      <div className="container">
        <div className="">
          <div className="centerr text-align-center">
            <p className="introduce-text1">معرفی کسب‌وکار به گیشنیز</p>
            <p className="introduce-text2">
              اطلاعات مربوط به کسب‌وکار را وارد کنید.
            </p>
            <div className="centerr">
              <input
                ref={inputengName}
                name="engName"
                value={data.engName}
                onChange={simpleInputHandler}
                dir="ltr"
                // className="introduce-input1 introduce-placeholder"
                className={
                  errors.engName && touched.engName
                    ? "introduce-input1 introduce-placeholder mb-md-1 border-error"
                    : "introduce-input1 introduce-placeholder mb-md-1"
                }
                type="text"
                placeholder="نام کسب و کار به انگلیسی"
              />

              {errors.engName && touched.engName && (
                <span className="ib-input-error">{errors.engName}</span>
              )}
            </div>
            <div className="centerr">
              <input
                name="perName"
                value={data.perName}
                onChange={simpleInputHandler}
                className="introduce-input1 introduce-placeholder"
                type="text"
                placeholder="نام کسب و کار فارسی"
              />
            </div>
            <div className="centerr">
              <div className="ib-3input justify-content-between d-flex">
                <div className="centerr auto3">
                  <Autocomplete
                    // value={data.countryy}
                    // onChange={(event, newValue) => {
                    //   setData({ ...data, countryy: newValue.Title });
                    // }}
                    value={valueCountry}
                    onChange={(event, newValue) => {
                      setValueCountry(newValue);
                      setProvinces([]);
                      setCities([]);
                      setValueProvince(null);
                      setValueCity(null);
                      setCountryId(newValue.CId);
                      setInputDisable(false);
                      setProvinceId("");
                    }}
                    inputValue={inputValueCountry}
                    onInputChange={(event, newInputValue) => {
                      setInputValueCountry(newInputValue);
                      setData({
                        ...data,
                        countryy: newInputValue,
                      });
                    }}
                    popupIcon={
                      <img src={iconn} className="icon-ib" alt="category img" />
                    }
                    // open={true}
                    disableClearable
                    disablePortal
                    openOnFocus={true}
                    id="auto3"
                    options={countries}
                    getOptionLabel={(option) => option.CountryName}
                    sx={{
                      fontFamily: "inherit",
                    }}
                    renderInput={(params) => (
                      <div className="d-flex">
                        <TextField
                          ref={inputcountryy}
                          name="countryy"
                          id="auto3"
                          className={
                            errors.countryy && touched.countryy
                              ? "auto-error"
                              : "auto-bord"
                          }
                          {...params}
                          placeholder="کشور"
                        ></TextField>
                      </div>
                    )}
                  />
                </div>
                <div className="centerr auto3">
                  <Autocomplete
                    // value={data.provincee}
                    // onChange={(event, newValue) => {
                    //   setData({ ...data, provincee: newValue.Title });
                    // }}

                    value={valueProvince}
                    onChange={(event, newValue) => {
                      console.log(newValue.Id);
                      setProvinceId(newValue.Id);
                      console.log(ProvinceId);

                      setValueProvince(newValue);
                      setCities([]);
                      setValueCity(null);
                    }}
                    inputValue={inputValueProvince}
                    onInputChange={(event, newInputValue) => {
                      setInputValueProvince(newInputValue);
                      setData({
                        ...data,
                        provincee: newInputValue,
                      });
                    }}
                    popupIcon={
                      <img src={iconn} className="icon-ib" alt="category img" />
                    }
                    // open={true}
                    disableClearable
                    disablePortal
                    openOnFocus={true}
                    id="auto4"
                    disabled={inputDisable}
                    options={provinces}
                    getOptionLabel={(option) => option.ProvinceName}
                    sx={{
                      fontFamily: "inherit",
                    }}
                    renderInput={(params) => (
                      <div className="d-flex">
                        <TextField
                          disabled={inputDisable}
                          ref={inputprovincee}
                          id="auto4"
                          className={
                            errors.provincee && touched.provincee
                              ? "auto-error"
                              : "auto-bord"
                          }
                          {...params}
                          placeholder="استان"
                        ></TextField>
                      </div>
                    )}
                  />
                </div>

                <div className=" auto3">
                  <Autocomplete
                    value={valueCity}
                    onChange={(event, newValue) => {
                      setValueCity(newValue);
                    }}
                    inputValue={inputValueCity}
                    onInputChange={(event, newInputValue) => {
                      setInputValueCity(newInputValue);
                      setData({ ...data, cityy: newInputValue });
                    }}
                    popupIcon={
                      <img src={iconn} className="icon-ib" alt="category img" />
                    }
                    // open={true}
                    disableClearable
                    disablePortal
                    openOnFocus={true}
                    disabled={inputDisable}
                    id="auto5"
                    options={cities}
                    getOptionLabel={(option) => option.CityName}
                    sx={{
                      fontFamily: "inherit",
                    }}
                    renderInput={(params) => (
                      <div className="d-flex">
                        <TextField
                          disabled={inputDisable}
                          ref={inputcityy}
                          id="auto5"
                          className={
                            errors.cityy && touched.cityy
                              ? "auto-error"
                              : "auto-bord"
                          }
                          {...params}
                          placeholder="شهر"
                        ></TextField>
                      </div>
                    )}
                  />
                </div>
              </div>
              {((errors.countryy && touched.countryy) ||
                (errors.provincee && touched.provincee) ||
                (errors.cityy && touched.cityy)) && (
                <span className="ib-input-error">
                  لطفا این قسمت را خالی نگذارید.
                </span>
              )}
            </div>
            <div className="centerr">
              <input
                dir="ltr"
                name="addresss"
                value={data.addresss}
                onChange={simpleInputHandler}
                className="introduce-input1 introduce-placeholder"
                type="text"
                placeholder="آدرس"
              />
            </div>

            {/* get input value for send to server */}
            {/* 
          {Object.keys(inputValues).map((c) => {
            return <p key={c}>{inputValues[c]}</p>;
          })} */}

            {Array.from(Array(counter)).map((c, index) => {
              return (
                <div className="centerr" key={index}>
                  <input
                    dir="ltr"
                    value={data.webOrInsta[index]}
                    //  {...console.log(inputValues[index])}
                    onChange={handleOnChange}
                    key={c}
                    id={index}
                    className="introduce-input1 introduce-placeholder"
                    type="text"
                    // placeholder="آدرس وب‌سایت یا اینستاگرام"
                    placeholder="آدرس وب‌سایت یا سوشال مدیا"
                    // value={}
                  />
                </div>
              );
            })}

            {/* <div className="d-flex justify-content-center">
              <div className="introduce-add1">
                {showCounter ? (
                  <button
                    className="introduce-add hov-shadow-btn "
                    onClick={handleClick}
                  >
                    <img src={add1} alt="add" className="introduce-add-img" />
                  </button>
                ) : (
                  ""
                )}
              </div>
            </div> */}

            <p className="introduce-text3">
              {" "}
              دسته‌بندی مربوط به کسب و کار را انتخاب کنید.
            </p>
            <div className="centerr">
              <div className=" auto6">
                <Autocomplete
                  value={value}
                  onChange={(event, newValue) => {
                    setChangedCid(!changedCid);
                    setItems([]);
                    setbuttonGroupsId([]);
                    setValue(newValue);
                    setChildCat([]);
                    setlocakChild(true);
                    setcid(newValue.CId);
                    listHandler(newValue);
                    setShowBtns(true);
                    setData({ ...data, category: newValue.Title });
                  }}
                  inputValue={inputValue}
                  onInputChange={(event, newInputValue) => {
                    setInputValue(newInputValue);
                    setData({
                      ...data,
                      categoryy: newInputValue,
                    });
                  }}
                  disabled={list3.length > 0}
                  popupIcon={
                    <img src={iconn} className="icon-ib" alt="category img" />
                  }
                  // open={true}
                  disableClearable
                  disablePortal
                  openOnFocus={true}
                  id="auto6"
                  options={categories}
                  getOptionLabel={(option) => option.Title}
                  className=""
                  sx={{
                    fontFamily: "inherit",
                  }}
                  renderInput={(params) => (
                    <div className="d-flex">
                      <TextField
                        id="auto6"
                        className=""
                        {...params}
                        placeholder=""
                      >
                        {" "}
                      </TextField>
                    </div>
                  )}
                />
              </div>
            </div>

            {list3.map((item) => (
              <Chip
                id="rb-chip"
                // dir="ltr"
                label={item}
                onDelete={() => {
                  if (list3.length == 1) {
                    deleteHandler(item);
                    setShowBtns(false);
                    // setOther(false);
                  } else {
                    deleteHandler(item);
                  }
                }}
                deleteIcon={
                  <img src={close} alt="X" className="s2-chip-icon" />
                }
                key={item}
              />
            ))}

            {/* second part */}
            {/* <div className={showBtns ? "d-block centerr" : "d-none centerr"}>
              <ButtonGroupBusinness />
            </div> */}
            <div className={showBtns ? "d-block rb2-bgb" : "d-none"}>
              {dataCome2 ? (
                childCat.length > 0 && (
                  <ButtonGroupBusinness2
                    itemss={items}
                    data={childCat}
                    buttonGroupsId={buttonGroupsId}
                    onChangebuttonGroupsId={setbuttonGroupsId}
                    groupValidate={groupValidate}
                    setGroupValidate={setGroupValidate}
                    showGV={showGV}
                    CatIdsApi={CatIdsApi}
                    setCatIdsApi={setCatIdsApi}
                    emptyInput={emptyInput}
                    setemptyInput={setemptyInput}
                  />
                )
              ) : (
                <>
                  <div className="d-flex justify-content-center  mt-5">
                    <Skeleton
                      id="rb-chip-skeleton"
                      variant="rectangular"
                      animation="wave"
                    />
                    <Skeleton
                      id="rb-chip-skeleton"
                      variant="rectangular"
                      animation="wave"
                    />
                    <Skeleton
                      id="rb-chip-skeleton"
                      variant="rectangular"
                      animation="wave"
                    />
                    <Skeleton
                      id="rb-chip-skeleton"
                      variant="rectangular"
                      animation="wave"
                    />
                  </div>
                  <div className="d-flex justify-content-center ">
                    <Skeleton
                      id="rb-chip-skeleton"
                      variant="rectangular"
                      animation="wave"
                    />
                    <Skeleton
                      id="rb-chip-skeleton"
                      variant="rectangular"
                      animation="wave"
                    />
                    <Skeleton
                      id="rb-chip-skeleton"
                      variant="rectangular"
                      animation="wave"
                    />
                    <Skeleton
                      id="rb-chip-skeleton"
                      variant="rectangular"
                      animation="wave"
                    />
                  </div>
                </>
              )}
            </div>

            <div className="centerr">
              <MDBBtn
                className="introduce-sabt-btn hov-shadow-btn shadow-off"
                onClick={submitHandler}
              >
                ثبت
              </MDBBtn>
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
};

export default IntroduceBusiness;
