import React, { useState, useEffect, useContext } from "react";
import { MDBRow } from "mdb-react-ui-kit";
import Autocomplete from "@mui/material/Autocomplete";
import iconn from "../../../images/next-left.png";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import TextField from "@mui/material/TextField";
import rtlPlugin from "stylis-plugin-rtl";
import { prefixer } from "stylis";
import { CacheProvider } from "@emotion/react";
import createCache from "@emotion/cache";
import Skeleton from "@mui/material/Skeleton";
import axios from "axios";
import "../indexprofile.css";

import { DomainContext } from "../../../App";
//components
import Sidbar from "../../../components/Dashbord/sidbar/Sidbar";
import Main from "../../../components/Dashbord/Main/Main";
import Header_mobile from "../../../components/Dashbord/Header_mobile/Header_mobile";
import AlertSuccess from "../../../components/Dashbord/AlertSuccess/AlertSuccess";

const theme = createTheme({
  direction: "rtl", // Both here and <body dir="rtl">
});
// Create rtl cache
const cacheRtl = createCache({
  key: "muirtl",
  stylisPlugins: [prefixer, rtlPlugin],
});

const Profilefirst = (props) => {
  // for domain of Apis
  const domain = useContext(DomainContext);

  const [apiCome, setApiCome] = useState(false);

  const [disable, setDisable] = useState(false);
  const [apiErr, setApiErr] = useState("");

  const [gender, setgender] = useState("");
  const [gendersList, setGendersList] = useState([]);
  const [gValue, setGValue] = useState(null);
  const [gInputValue, setGInputValue] = useState("");
  const [countryList, setCountryList] = useState([]);
  const [cValue, setCValue] = useState(null);
  const [cInputValue, setCInputValue] = useState("");
  const [profileDetail, setProfileDetail] = useState({
    MId: "",
    FirstName: "",
    LastName: "",
    UserName: "",
    Email: "",
    Gender: 0,
    DefaultCountry: 0,
  });

  const inputChangeHandler = (event) => {
    setProfileDetail({
      ...profileDetail,
      [event.target.name]: event.target.value,
    });
  };

  const [showAlert, setshowAlert] = useState(false);

  const alertHandeler = () => {
    setshowAlert(true);
    setTimeout(() => {
      // After 3 seconds set the show value to false
      setshowAlert(false);
    }, 4000);
  };

  //api header
  const header = {
    ApiKey: "87e4d3a91a554a2196ae8bdf0bfdcdc9",
    MemId: getCookie("MemId"),
  };
  useEffect(() => {
    if (getCookie("MemId")) {
      axios
        // .get("https://api.gishnizapp.ir/api/member/detail", {
        .get(domain + "/api/member/detail", {
          headers: header,
        })
        .then((response) => {
          console.log("response");
          console.log(response.data);
          if ((response.data.msg = "done")) {
            setGendersList(response.data.Gender);
            setCountryList(response.data.CountryList);
            setProfileDetail(response.data.ProfileDetail);
            if (response.data.ProfileDetail.Gender != 0) {
              var id = response.data.ProfileDetail.Gender;
              var arr = response.data.Gender;
              for (let index = 0; index < arr.length; index++) {
                if (arr[index].GId == id) {
                  setGValue(arr[index]);
                }
              }
            }
            if (response.data.ProfileDetail.DefaultCountry != 0) {
              var id = response.data.ProfileDetail.DefaultCountry;
              var arr = response.data.CountryList;
              for (let index = 0; index < arr.length; index++) {
                if (arr[index].Id == id) {
                  setCValue(arr[index]);
                }
              }
            }
            setApiCome(true);
          }
        })
        .catch((error) => {
          // alert(error);
        });
    } else {
      props.history.push({
        pathname: "/login",
      });
    }
  }, []);

  function getCookie(cname) {
    let name = cname + "=";
    let decodedCookie = decodeURIComponent(document.cookie);
    let ca = decodedCookie.split(";");
    for (let i = 0; i < ca.length; i++) {
      let c = ca[i];
      while (c.charAt(0) == " ") {
        c = c.substring(1);
      }
      if (c.indexOf(name) == 0) {
        return c.substring(name.length, c.length);
      }
    }
    return "";
  }

  const headerPost = {
    ApiKey: "87e4d3a91a554a2196ae8bdf0bfdcdc9",
  };

  const submitHandler = (event) => {
    event.preventDefault();
    setDisable(true);
    setApiErr("");

    const dataPost = {
      MId: getCookie("MemId"),
      FirstName: profileDetail.FirstName,
      LastName: profileDetail.LastName,
      Gender: profileDetail.Gender,
      DefaultCountry: profileDetail.DefaultCountry,
    };
    console.log(dataPost);

    axios
      .post(domain + "/api/member/save-profile", dataPost, {
        headers: headerPost,
      })
      .then((response) => {
        setDisable(false);
        console.log(response.data);
        if (response.data.msg == "done") {
          alertHandeler();
        } else if (response.data.msg == "error") {
          setApiErr("خطا در ثبت اطلاعات لطفا دوباره امتحان کنید.");
        } else if (response.data.msg == "fill-all") {
          setApiErr("لطفا همه موارد را پر کنید.");
        }
      })
      .catch((error) => {
        // alert(error);
      });
  };

  return (
    <>
      <Header_mobile />

      <div>
        {" "}
        <CacheProvider value={cacheRtl}>
          <ThemeProvider theme={theme}>
            <div className="margin-0 d-flex">
              <div className="border-left-dashbord d-none d-md-block">
                <Sidbar />
              </div>
              <div className="bg-main-dashbord">
                <Main lencomments={2}>
                  <h5 className="header-main d-none d-md-block">
                    ویرایش اطلاعات پروفایل گیشنیز
                  </h5>
                  <h5 className="header-main d-block d-md-none">پروفایل</h5>

                  {apiCome ? (
                    <>
                      <div className="centerr  mt-3 mt-md-0">
                        <TextField
                          disabled
                          name="Email"
                          value={profileDetail.Email}
                          onChange={inputChangeHandler}
                          id="outlined-basic"
                          label="ایمیل"
                          variant="outlined"
                          className={
                            "label-field-less5 custom-text-field custom-text-field-dash mb-0 dash-text-field"
                          }
                        />
                      </div>
                      <div className="centerr mt15">
                        <TextField
                          disabled
                          name="UserName"
                          value={profileDetail.UserName}
                          onChange={inputChangeHandler}
                          id="outlined-basic"
                          label="نام کاربری"
                          variant="outlined"
                          className={
                            "label-field-less5 custom-text-field custom-text-field-dash mb-0 dash-text-field"
                          }
                        />
                      </div>

                      {/* inputs */}
                      <div className="row-profilefirst d-md-flex mx-auto mt15">
                        <TextField
                          name="FirstName"
                          value={profileDetail.FirstName}
                          onChange={inputChangeHandler}
                          id="outlined-basic"
                          label="نام"
                          variant="outlined"
                          className={
                            "label-field-less5 custom-text-field custom-text-field-dash mb-0 ms-md-2 dash-text-field"
                          }
                        />
                        <div className="mt15"></div>

                        <TextField
                          name="LastName"
                          value={profileDetail.LastName}
                          onChange={inputChangeHandler}
                          id="outlined-basic"
                          label="نام خانوادگی"
                          variant="outlined"
                          className={
                            "label-field-less5 custom-text-field custom-text-field-dash mb-0 me-md-2 dash-text-field"
                          }
                        />
                      </div>

                      {/* auto compelete */}
                      <div className="row-profilefirst d-md-flex mx-auto">
                        <div
                          dir="rtl"
                          className="mb-3 mb-md-0 ms-md-2 width-100"
                        >
                          <div className="d-md-flex">
                            <div className=" auto10 width-100 ">
                              <Autocomplete
                                value={gValue}
                                onChange={(event, newValue) => {
                                  setGValue(newValue);
                                  setProfileDetail({
                                    ...profileDetail,
                                    Gender: newValue.GId,
                                  });
                                }}
                                inputValue={gInputValue}
                                onInputChange={(event, newInputValue) => {
                                  setGInputValue(newInputValue);
                                }}
                                className="costum-autocompelette-profile"
                                popupIcon={
                                  <img
                                    src={iconn}
                                    className="icon-pro-auto"
                                    alt="category img"
                                  />
                                }
                                disableClearable
                                disablePortal
                                openOnFocus={true}
                                options={gendersList}
                                getOptionLabel={(option) => option.Title}
                                sx={{
                                  fontFamily: "inherit",
                                }}
                                renderInput={(params) => (
                                  <div className="d-flex width-100">
                                    <TextField
                                      {...params}
                                      label="جنسیت"
                                      className="label-field-more "
                                    >
                                      {" "}
                                    </TextField>
                                  </div>
                                )}
                              />
                            </div>
                          </div>
                        </div>

                        <div
                          dir="rtl"
                          className="mb-3 mb-md-0 me-md-2 width-100"
                        >
                          <div className="d-md-flex">
                            <div className=" auto10 width-100">
                              <Autocomplete
                                // open={true}
                                value={cValue}
                                onChange={(event, newValue) => {
                                  console.log("c newValue");
                                  console.log(newValue);
                                  setCValue(newValue);
                                  setProfileDetail({
                                    ...profileDetail,
                                    DefaultCountry: newValue.Id,
                                  });
                                  // setData({
                                  //   ...data,
                                  //   categoryy: newValue.Title,
                                  //   // objectValue: newValue,
                                  // });
                                }}
                                inputValue={cInputValue}
                                onInputChange={(event, newInputValue) => {
                                  console.log("cInputValue");
                                  console.log(cInputValue);
                                  setCInputValue(newInputValue);
                                  // setData({ ...data, categoryy: newInputValue });
                                }}
                                className="costum-autocompelette-profile"
                                popupIcon={
                                  <img
                                    src={iconn}
                                    className="icon-pro-auto"
                                    alt="category img"
                                  />
                                }
                                disableClearable
                                disablePortal
                                openOnFocus={true}
                                options={countryList}
                                getOptionLabel={(option) => option.CountryTitle}
                                sx={{
                                  fontFamily: "inherit",
                                }}
                                renderInput={(params) => (
                                  <div className="d-flex width-100">
                                    <TextField
                                      {...params}
                                      label="کشور پیش فرض"
                                      className=" label-field-more12"
                                    >
                                      {" "}
                                    </TextField>
                                  </div>
                                )}
                              />
                            </div>
                          </div>
                        </div>
                      </div>
                      <div className="centerr mt15 input-error-dash text-center">
                        {apiErr}
                      </div>
                    </>
                  ) : (
                    <>
                      <div className="centerr  mt-3 mt-md-0">
                        <Skeleton
                          className="profile-Skeleton"
                          variant="text"
                          animation="wave"
                        />
                      </div>
                      <div className="centerr mt-n13">
                        <Skeleton
                          className="profile-Skeleton"
                          variant="text"
                          animation="wave"
                        />
                      </div>
                      <div className="row-profilefirst d-md-flex mx-auto mt-n13">
                        <Skeleton
                          className="profile-Skeleton ms-md-2"
                          variant="text"
                          animation="wave"
                        />
                        <div className="mt-n13"></div>
                        <Skeleton
                          className="profile-Skeleton me-md-2"
                          variant="text"
                          animation="wave"
                        />
                      </div>
                      <div className="row-profilefirst d-md-flex mx-auto mt-n13">
                        <Skeleton
                          className="profile-Skeleton ms-md-2"
                          variant="text"
                          animation="wave"
                        />
                        <div className="mt-n13"></div>
                        <Skeleton
                          className="profile-Skeleton me-md-2"
                          variant="text"
                          animation="wave"
                        />
                      </div>
                      {/* <div className="row-profilefirst d-md-flex mx-auto">
                        <div
                          dir="rtl"
                          className="mb-3 mb-md-0 ms-md-2 width-100"
                        >
                          <div className="d-md-flex">
                            {" "}
                            <Skeleton
                              className="profile-Skeleton"
                              variant="text"
                              animation="wave"
                            />{" "}
                            <Skeleton
                              className="profile-Skeleton"
                              variant="text"
                              animation="wave"
                            />
                          </div>
                        </div>
                      </div> */}
                    </>
                  )}

                  {/* save button */}
                  <MDBRow className="row_saveButton">
                    <div>
                      <button
                        disabled={disable}
                        onClick={submitHandler}
                        className="saveButton saveButton1 "
                      >
                        ذخیره اطلاعات
                      </button>
                    </div>
                  </MDBRow>
                </Main>
              </div>
            </div>
          </ThemeProvider>
        </CacheProvider>
      </div>
      {/* alert delete */}
      <AlertSuccess
        showAlert={showAlert}
        text={" اطلاعات شما با موفقیت ثبت شد."}
      />
    </>
  );
};

const genders = [
  { label: "مرد", year: 1 },
  { label: "زن", year: 2 },
  { label: "دیگر", year: 3 },
];

const countries = [
  { label: "کانادا", year: 1 },
  { label: "آمریکا", year: 2 },
];

export default Profilefirst;
