import React from "react";
import { MDBContainer, MDBIcon } from "mdb-react-ui-kit";
import { Link } from "react-router-dom";
import Skeleton from "@mui/material/Skeleton";
import "./Listmobile.css";

// import kharid from "../../images/category/مرکز خرید و فروشگاه.png";
// import resturan from "../../images/category/رستوران و کافی‌شاپ.png";
// import pezeshki from "../../images/category/پزشکی و مشاوره.png";
// import vekalat from "../../images/category/وکالت و امور حقوقی.png";
// import zibayi from "../../images/category/زیبایی.png";
// import khadamat from "../../images/category/خدمات منزل.png";
// import bime from "../../images/category/خدمات بیمه و مالی.png";
import bishtar from "../../images/بیشتر ریسپانسیور.png";

const Listmobile = (props) => {
  return (
    <div className="list-rec">
      <MDBContainer fluid className="px-0 w-285">
        <div className="row ">
          {props.data.map((item) =>
            props.dataCome ? (
              <div className="listmobile-center list2 col-3">
                <Link className="a-listmobile " to={item.Url}>
                  <img
                    className="img-listmobile mb-1"
                    src={item.Icon}
                    alt={item.ShortTitle}
                  />
                  <br />
                  {item.ShortTitle}
                </Link>
              </div>
            ) : (
              <div className="listmobile-center list2 col-3">
                <Link className="a-listmobile ">
                  <Skeleton
                    variant="circular"
                    className="mx-auto"
                    width={40}
                    height={40}
                    animation="wave"
                  />
                  <Skeleton
                    className="mx-0 mt-3"
                    height={12}
                    animation="wave"
                  />
                </Link>
              </div>
            )
          )}

          <div className="listmobile-center list2 col-3">
            <Link className="a-listmobile " to="/result">
              <img className="img-listmobile mb-1" src={bishtar} alt="بیشتر" />
              <br />
              بیشتر
            </Link>
          </div>
        </div>
      </MDBContainer>
    </div>
  );
};

export default Listmobile;
