import React, { useEffect, useState ,useContext } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import { MDBFooter, MDBIcon } from "mdb-react-ui-kit";

import facebook from "../../images/home-body/facebook foter.png";
import youtube from "../../images/home-body/youtube foter.png";
import instagram from "../../images/home-body/instagram foter.png";
import twitter from "../../images/home-body/twitter foter.png";
import logo from "../../images/home-body/logo gishniz.png";

import{DomainContext} from '../../App';
const Footer = () => {
  // for domain of Apis
  const domain = useContext(DomainContext);

  const [country, setCountry] = React.useState([{}, {}]);
  const [language, setLanguage] = React.useState([{}, {}]);
  const [menu, setMenu] = React.useState([
    {
      ColumnTitle: "",
      Items: [
        { Link: "1", Title: "" },
        { Link: "2", Title: "" },
      ],
    },
    {
      ColumnTitle: "",
      Items: [
        { Link: "1", Title: "" },
        { Link: "2", Title: "" },
      ],
    },
  ]);

  const handleChange2 = (event) => {
    setCountry(event.target.value);
  };

  const [data, setData] = React.useState({
    FooterLogo: { Link: "", Title: "" },
    SocialMediaList : [{}]
  });

  //api header
  const header = {
    ApiKey: "87e4d3a91a554a2196ae8bdf0bfdcdc9",
    LangId: "1",
  };

  useEffect(() => {
    axios
      .get(domain+"/api/footer", { headers: header })
      .then((response) => {
        setData(response.data);
        setLanguage(response.data.LanguageList);
        setCountry(response.data.CountryList);
        setMenu(response.data.FooterMenuItems);
      })
      .catch((error) => {
        // alert(error);
      });
  }, []);

  return (
    <>
      <MDBFooter className="text-center text-lg-start text-white bg-282828 height-287 ">
        <section className=" container text-center py-2 ">
          <div className="py-1">
            {data.SocialMediaList.map((item) => (
              <a href={item.SMLink} key={item.SMTitle} className="mx-10px text-reset foot-hover">
                <img
                  className="footer-icon"
                  src={item.Icon}
                  alt={item.SMTitle}
                />
              </a>
            ))}
            {/* <a href="" className="mx-10px text-reset foot-hover">
              <img className="footer-icon" src={youtube} alt="youtube" />
            </a>
            <a href="" className="mx-10px text-reset foot-hover">
              <img className="footer-icon" src={twitter} alt="twitter" />
            </a>
            <a href="" className="mx-10px text-reset foot-hover">
              <img className="footer-icon" src={instagram} alt="instagram" />
            </a>
            <a href="" className="mx-10px text-reset foot-hover">
              <img className="footer-icon" src={facebook} alt="facebook" />
            </a> */}
          </div>
        </section>
        <hr className="my-0" />

        <section className="">
          <div className="container text-end text-md-end mt-3">
            <div className="row pe-3 pe-lg-0">
              <div className="col-6 col-sm-3 col-md-2 col-lg-2 col-xl-2 fs-14 fs-xs-14 mx-auto mt-2 ">
                <p className="fw-bold mb-3 main-font-color">
                  {menu[0].ColumnTitle}
                </p>
                <div className="mt-1 mb-sm-4 ">
                  {menu[0].Items.map((item) => (
                    <p className="mb-1" key={item.Title}>
                      <a href={item.Link} className="foot-hover ">
                        {item.Title}
                      </a>
                    </p>
                  ))}
                  {/* <p className="mb-1">
                    <Link to="/aboutus" className="foot-hover ">
                      درباره ما
                    </Link>
                  </p>
                  <p className="mb-1">
                    <Link to="/contact" className="foot-hover ">
                      تماس با ما
                    </Link>
                  </p>
                  <p className="mb-1">
                    <Link to="/rules" className="foot-hover ">
                      قوانین و مقررات
                    </Link>
                  </p>
                  <p className="mb-1">
                    <Link to="/privacy" className="foot-hover ">
                      حریم شخصی
                    </Link>
                  </p>
                  <p className="mb-1">
                    <a href="#!" className="foot-hover ">
                      بلاگ
                    </a>
                  </p>
                  <p className="mb-1">
                    <a href="#!" className="foot-hover ">
                      سوالات متداول
                    </a>
                  </p> */}
                </div>
              </div>

              <div className="col-6 col-sm-4 col-md-3 col-lg-2 col-xl-2 mx-auto fs-14 fs-xs-14 mt-2 ">
                <p className="fw-bold mb-3 main-font-color ">
                  {menu[1].ColumnTitle}
                </p>
                <div className="mt-1">
                  {menu[1].Items.map((item) => (
                    <p className="mb-1" key={item.Title}>
                      <a href={item.Link} className="foot-hover">
                        {item.Title}
                      </a>
                    </p>
                  ))}
                  {/* <p className="mb-1">
                    <Link to="/signup" className="foot-hover">
                      ثبت نام در گیشنیز
                    </Link>
                  </p>
                  <p className="mb-1">
                    <a
                      href="https://biz.gishniz.com/login"
                      className="foot-hover"
                    >
                      ادعای مالکیت کسب و کار
                    </a>
                  </p>
                  <p className="mb-1">
                    <a
                      href="https://biz.gishniz.com/business-dashboard"
                      className="foot-hover"
                    >
                      پنل صاحبان کسب و کار
                    </a>
                  </p>
                  <p className="mb-1">
                    <Link to="/advertise" className="foot-hover">
                      تبلیغات در گیشنیز
                    </Link>
                  </p> */}
                </div>
              </div>

              <div className="col-0 col-sm-3 col-md-5 col-lg-7 col-xl-7 mx-auto "></div>

              <div className="col-12 col-sm-2 col-md-2 col-lg-1 col-xl-1 mx-auto  mt-0 mt-sm-5 pe-lg-0">
                <div className="mb-3 mb-sm-4 mt-4 ">
                  <h6 className=" fw-bold fs-12 fs-xs-12 main-font-color ">
                    انتخاب زبان
                  </h6>
                  <select
                    // onChange={handleChange2}
                    id="country"
                    className="select-footer bg-282828 text-white fs-12 fs-xs-12  "
                    icon="camera-retro"
                  >
                    {language.map((item) => (
                      <option key={item.LId} value={item.LanguageTitle}>
                        {item.LanguageTitle}
                      </option>
                    ))}
                    {/* <option value="canada">America</option> */}
                  </select>
                </div>
                <div className="mb-sm-4 mb-4">
                  <h6 className=" fw-bold fs-12 fs-xs-12 main-font-color">
                    انتخاب کشور
                  </h6>
                  <select
                    // onChange={handleChange2}
                    id="country"
                    className="select-footer bg-282828 text-white fs-12 fs-xs-12  "
                    icon="camera-retro"
                  >
                    {country.map((item) => (
                      <option key={item.CId} value={item.CountryName}>
                        {item.CountryName}
                      </option>
                    ))}
                  </select>
                </div>
              </div>
            </div>
          </div>
        </section>
      </MDBFooter>

      <div className="text-center bg-white color-242B2E height-91 d-flex align-self-center ">
        <div className="container align-self-center ">
          <div className="row ">
            <div className="col-8 col-sm-8 align-self-center text-end">
              <p className="m-0 fs-14 fs-xs-12 fw-light pe-3 pe-lg-0">
                کلیه حقوق مادی و معنوی این سایت متعلق به گروه ویکیفا می‌باشد.
              </p>
            </div>
            <div className="col-4 col-sm-4   text-start">
              <p className="m-0 fs-8 align-self-center ps-lg-0 ps-md-5 ps-2 ps-sm-4">
                <Link className="" to="/">
                  <img
                    // src={logo}
                    src={data.FooterLogo.Link}
                    alt={data.FooterLogo.Title}
                    title=""
                    height="25px"
                    className="mt-10 mb-2 footer-logo ms-1"
                  />
                </Link>
                <br></br>
                Copyright © 2022 Gishniz
              </p>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Footer;
