import React from "react";
import { Link } from "react-router-dom";
import Footer from "../components/footer/Footer";
// import { MDBBtn } from "mdb-react-ui-kit";

import logo from "../images/home-body/logo gishniz.png";
import closeIcon from "../images/header/close.png";

const IntroduceBusinessThanks = (props) => {
  // const { state } = props.location;
  // console.log(state)
  return (
    <>
    <div className="container">
      <div className="centerr">
        <Link to="/" className="ibt-logo-a">
          <img src={logo} alt="logo gishniz" className="ibt-logo" />
        </Link>

        <div className="ibt-box text-end">
          <Link to="/">
            <img
              className="ibt-close-img me-0"
              src={closeIcon}
              alt="close icon"
            />
          </Link>
          <div className="centerr">
            <p className="ibt-text1">کسب‌وکار با موفقیت ثبت شد.</p>
            <p className="ibt-text2">
              
              ممنون که 
              {/* ({state.engName})  */}
               را به ما معرفی کردی. اطلاعات این بیزینس پس
              از بررسی توسط تیم گیشنیز منتشر خواهد شد.
            </p>
          </div>
        </div>

        {/* <MDBBtn className="ibt-sabt-btn hov-shadow-btn shadow-off"> */}
        <div>

          <Link to="/introduce-business" className="ibt-sabt-at hov-shadow-btn shadow-off">
            معرفی کسب‌وکارهای بیشتر
          </Link>
        {/* </MDBBtn> */}
        </div>
      </div>
    </div>
    <Footer />
    </>
  );
};

export default IntroduceBusinessThanks;
