import React, { useState } from "react";
import {
  MDBBtn,
  MDBModal,
  MDBModalDialog,
  MDBModalContent,
  MDBModalHeader,
  MDBModalTitle,
  MDBModalBody,
  MDBModalFooter,
} from "mdb-react-ui-kit";
import "./result-modal2.css";
import Box from "@mui/material/Box";
import Autocomplete from "@mui/material/Autocomplete";
import TextField from "@mui/material/TextField";

import address from "../../images/result/address red.png";

const ResultModal = () => {
  const [basicModal, setBasicModal] = useState(false);

  const toggleShow = () => setBasicModal(!basicModal);
  return (
    <>
      <MDBBtn onClick={toggleShow} className="result-modal-launch-btn">
  

        <img src={address} alt="" className="result-city-img1" />
              لطفا شهر خود را انتخاب کنید
            
      </MDBBtn>
      <MDBModal show={basicModal} setShow={setBasicModal} tabIndex="-1">
        <MDBModalDialog size="fullscreen">
          <MDBModalContent>
            <MDBModalHeader className="result-modal-header">
              <MDBModalTitle>لطفا شهر خود را انتخاب کنید</MDBModalTitle>
              <MDBBtn
                className="btn-close mx-0"
                color="none"
                onClick={toggleShow}
              ></MDBBtn>
            </MDBModalHeader>
            <MDBModalBody className="p-0">

            <div className="result-filter">
              
              <div className="auto2 p-0 mx-4 mt-4">
                    <Autocomplete
                      popupIcon={""}
                      // open={true}

                      disableClearable
                      disablePortal
                      id="combo-box-demo0"
                      options={cities}
                      sx={{ width: 300 }}
                      getOptionLabel={(option) => option.label}
                      renderOption={(props, option) => (
                        <Box
                          component="li"
                          sx={{ "& > img": { ml: 2, flexShrink: 0 } }}
                          className="fs-4vw"
                          {...props}
                        >
                          {/* <img
                            src={searchBlack}
                            alt="search black"
                            className=" ms-2  autocomplete-search"
                          /> */}
                          {/* <img
                          loading="lazy"
                          width="20"
                          src={`https://flagcdn.com/w20/${option.code.toLowerCase()}.png`}
                          srcSet={`https://flagcdn.com/w40/${option.code.toLowerCase()}.png 2x`}
                          alt=""
                        /> */}
                          {option.label}
                        </Box>
                      )}
                      renderInput={(params) => (
                        <TextField
                          {...params}
                          placeholder="شهر"
                          className=""
                        />
                      )}
                    />
                  </div>
            </div>
            </MDBModalBody>

            <MDBModalFooter className="result-modal-footer">
              <MDBBtn onClick={toggleShow} className="result-modal-close-btn">
                اعمال فیلتر
              </MDBBtn>
            </MDBModalFooter>
          </MDBModalContent>
        </MDBModalDialog>
      </MDBModal>
    </>
  );
};
const cities = [
  { label: " تورنتو", id: 1 },
  { label: "تورنتو", id: 2 },
  { label: "تورنتو", id: 3 },
  { label: "تورنتو", id: 4 },
  { label: "تورنتو", id: 5 },
  // { label: "خودرو", id: 1980 },
  // { label: "ورزشی", id: 6 },
  // { label: "زیبایی", id: 7 },
  // { label: "دامپزشکی", id: 8 },
  // { label: "طراحی لباس", id: 9 },
  // { label: "انجمن", id: 10 },
];
export default ResultModal;
