import React, { useState } from "react";
import {
  MDBBtn,
  MDBModal,
  MDBModalDialog,
  MDBModalContent,
  MDBModalHeader,
  MDBModalTitle,
  MDBModalBody,
  MDBModalFooter,
} from "mdb-react-ui-kit";
import "./result-modal.css";
import SwitchFilter from "../switch-filter/SwitchFilter";
import RTLAccordian from "../rtl-accordian/RTLAccordian";
import filter from "../../images/result/filter.png";

const ResultModal = () => {
  const [basicModal, setBasicModal] = useState(false);

  const toggleShow = () => setBasicModal(!basicModal);
  return (
    <>
      <MDBBtn onClick={toggleShow} className="result-modal-launch-btn">
        <img src={filter} alt="" />
        فیلتر
      </MDBBtn>
      <MDBModal show={basicModal} setShow={setBasicModal} tabIndex="-1" dir="rtl">
        <MDBModalDialog size="fullscreen">
          <MDBModalContent>
            <MDBModalHeader className="result-modal-header">
              {/* <div className="d-flex justify-content-between"> */}
              <MDBModalTitle>فیلترها</MDBModalTitle>
              <MDBBtn
                className="btn-close mx-0"
                color="none"
                onClick={toggleShow}
              ></MDBBtn>
              {/* </div> */}
              {/* <div className="zzz"></div> */}
            </MDBModalHeader>
            <MDBModalBody className="p-0">

            <div className="result-filter">
              <div className="result-filter-div">
                <span>کسب‌وکار آنلاین</span>
                <SwitchFilter />
              </div>
              <div className="result-filter-div">
                <span>شبانه‌روزی</span>
                <SwitchFilter />
              </div>
              <div className="result-filter-div">
                <span>روزهای تعطیل هم باز است</span>
                <SwitchFilter />
              </div>
              <RTLAccordian />
            </div>
            </MDBModalBody>

            <MDBModalFooter className="result-modal-footer">
              <MDBBtn onClick={toggleShow} className="result-modal-close-btn">
                اعمال فیلتر
              </MDBBtn>
            </MDBModalFooter>
          </MDBModalContent>
        </MDBModalDialog>
      </MDBModal>
    </>
  );
};

export default ResultModal;
