import React, { useState, useEffect, useContext } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import { MDBRow, MDBCol, MDBBtn, MDBIcon } from "mdb-react-ui-kit";
import "./comments.css";
import Cardscommets from "./Cardscommets";
import RateIcon from "../rate-icon/RateIcon";
import nextg from "../../images/business/next.png";
import LoginModal from "../login-modal/LoginModal";
import { DomainContext } from "../../App";
import commentsIcon from "../../images/business/comments.png";

const Comments = (props) => {
  // for domain of Apis
  const domain = useContext(DomainContext);

  //give data from api
  const [data, setData] = useState({
    all: 0,
    awli: 0,
    khob: 0,
    motevaset: 0,
    zaief: 0,
    bad: 0,
    rate: 0,
  });

  const [more, setMore] = useState(false);

  const moreCommentHandler = () => {
    setMore(true);
  };

  const [com, setCom] = useState([]);

  //api header
  const header = {
    ApiKey: "87e4d3a91a554a2196ae8bdf0bfdcdc9",
    LangId: "1",
    BizId: props.id,
    MemberId: "9",
    Count: "1",
    LocId: "7",
  };

  useEffect(() => {
    axios
      .get(domain + "/api/bizrate", { headers: header })
      .then((response) => {
        setData({
          all: response.data.RateCount,
          awli: response.data.Rate5,
          khob: response.data.Rate4,
          motevaset: response.data.Rate3,
          zaief: response.data.Rate2,
          bad: response.data.Rate1,
          rate: response.data.Rate,
        });
      })
      .catch((error) => {
        // alert(error);
      });
    axios
      .get(domain + "/api/reviewforbiz", { headers: header })
      .then((response) => {
        setCom(response.data);
      })
      .catch((error) => {
        // alert(error);
      });
  }, []);

  function getCookie(cname) {
    let name = cname + "=";
    let decodedCookie = decodeURIComponent(document.cookie);
    let ca = decodedCookie.split(";");
    for (let i = 0; i < ca.length; i++) {
      let c = ca[i];
      while (c.charAt(0) == " ") {
        c = c.substring(1);
      }
      if (c.indexOf(name) == 0) {
        return c.substring(name.length, c.length);
      }
    }
    return "";
  }

  return (
    <>
      <h1 className=" text-main-nazar">امتیاز و دیدگاه کاربران</h1>

      <MDBRow className="mx-0 main-border">
        <MDBCol lg="4" className="padding-col4-comment">
          <MDBRow>
            {" "}
            <div className="d-flex align-items-center">
              <span className="f-22px ml--5 fw-bold">{data.rate}</span>
              <span className="f-12px fw-bold">از 5</span>
            </div>
            <div className="mb-3 pb-md-1">
              <span className="float-end">
                <RateIcon
                  cardrate={parseFloat(data.rate)}
                  isRec={0}
                  isAdd={0}
                  isComment1={1}
                  dir="ltr "
                />
              </span>
              <span className=" text1-comment">از مجموع {data.all} نفر</span>
            </div>
          </MDBRow>
          <div>
            <p className="mb--5">
              <small className="float-end comment-prog-text">عالی</small>
              <small className="float-start comment-prog-numb">
                {data.awli}
              </small>
            </p>
            <progress
              className="progress-color"
              value={data.awli}
              max={data.all}
            ></progress>
          </div>
          <div>
            <p className="mb--5">
              <small className="float-end comment-prog-text">خوب</small>
              <small className="float-start comment-prog-numb">
                {data.khob}
              </small>
            </p>

            <progress
              className="progress-color"
              value={data.khob}
              max={data.all}
            ></progress>
          </div>
          <div>
            <p className="mb--5">
              <small className="float-end comment-prog-text">متوسط</small>
              <small className="float-start comment-prog-numb">
                {data.motevaset}
              </small>
            </p>
            <progress
              className="progress-color"
              value={data.motevaset}
              max={data.all}
            ></progress>
          </div>
          <div>
            {" "}
            <p className="mb--5">
              <small className="float-end comment-prog-text">ضعیف</small>
              <small className="float-start comment-prog-numb">
                {data.zaief}
              </small>
            </p>
            <progress
              className="progress-color"
              value={data.zaief}
              max={data.all}
            ></progress>
          </div>
          <div>
            {" "}
            <p className="mb--5">
              <small className="float-end comment-prog-text">بد</small>
              <small className="float-start comment-prog-numb">
                {data.bad}
              </small>
            </p>
            <progress
              className="progress-color"
              value={data.bad}
              max={data.all}
            ></progress>
          </div>
          <p className="sabt-nazar-comment text-align-center">
            شما هم درباره این کسب‌و‌کار دیدگاه ثبت کنید
          </p>
          {/* <div className="centerr">
            <Link
              className="btn-nazar-comment hov-shadow"
              to=`/write-review?BizId=+`
            >
              ثبت نظر
            </Link>
          </div> */}

          {getCookie("MemId") ? (
            <div className="centerr">
              <Link
                className="btn-nazar-comment hov-shadow"
                to={`/write-review/${props.id}`}
              >
                ثبت نظر
              </Link>
            </div>
          ) : (
            <LoginModal id={3} url={props.url} text={"ثبت نظر"} />
          )}
        </MDBCol>
        <MDBCol lg="8" className="px-md-0 card-components-col">
          <div className="custom-hr d-md-none"></div>
          {console.log("com")}
          {console.log(com.length)}
          {com.length == 0 ? (
            <div className="com-empty-biz mt-4 mt-md-0">
              <div className="d-flex align-items-center justify-contet-center">
                <div className="text-align-center mx-auto">
                  <img
                    src={commentsIcon}
                    alt="comments"
                    className="img-com-empty-biz"
                  />
                  <p>شما اولین دیدگاه را ثبت کنید.</p>
                </div>
              </div>
            </div>
          ) : (
            com.map((item) => (
              <Cardscommets
                key={item.Fullname}
                rate={item.Rate}
                likes={item.Agree}
                name={item.Fullname}
                date={item.CommentDate}
                length={267}
                text={item.CommentBody}
                img={item.PhotoPath}
              />
            ))
          )}
          {/* <Cardscommets
            likes={2}
            length={267}
            text="It's a nice, sturdy little stand for a headset. The metal is actually stronger than I expected it to be, and there's rubber where it sits on the desk and where the headband of the headset rests on the stand, which is nice and keeps things where they're supposed to"
          />*/}

          <div className={more ? "d-block" : "d-none"}>
            {" "}
            <Cardscommets
              likes={2}
              length={267}
              text="It's a nice, sturdy little stand for a headset. The metal is actually stronger than I expected it to be, and there's rubber where it sits on the desk and where the headband of the headset rests on the stand, which is nice and keeps things where they're supposed to"
            />
          </div>
          {com.length > 4 ? (
            <MDBBtn className="a-arrow-comments" onClick={moreCommentHandler}>
              <span>دیدگاه‌های دیگر</span>
              <span className="arrow-comments">
                <img src={nextg} className="business-comment-more-img" alt="" />
              </span>
            </MDBBtn>
          ) : (
            <p></p>
          )}
        </MDBCol>
      </MDBRow>
    </>
  );
};

export default Comments;
