import React, { useEffect, useState } from "react";
import MetaTags from "react-meta-tags";

const MetaTagsSeo = (props) => {
  const [MetaTitle, setMetaTitle] = useState("");
  const [MetaTag, setMetaTag] = React.useState([]);
  const [MetaLink, setMetaLink] = React.useState([]);
  const [MetaSchema, setMetaSchema] = React.useState([]);
  const [MetaSchemaTag, setMetaSchemaTag] = React.useState({});
  const [ALink, setALink] = React.useState([]);
  const [MSTags, setMSTags] = React.useState([]);

  useEffect(() => {
    if (props.data) {
      setMetaTitle(props.data.Title);
      setMetaLink(props.data.Link);
      setMetaTag(props.data.Tags);
      setMetaSchemaTag(props.data.SchemaTag);
      setMetaSchema(props.data.Schema);
      setALink(props.data.aLink);
      setMSTags(props.data.msTags);
    }
  }, []);

  return (
    <div className="wrapper">
      <MetaTags>
        <title>{MetaTitle}</title>

        {/* Meta Tags */}
        {MetaTag != null &&
          MetaTag.map((item) =>
            item.type == "name" ? (
              <meta name={item.name} content={item.content} />
            ) : item.type == "property" ? (
              <meta property={item.name} content={item.content} />
            ) : (
              ""
            )
          )}

        {/* Links */}
        {MetaLink != null &&
          MetaLink.map((item) => <link rel={item.name} href={item.href} />)}

        {/* A Links */}
        {ALink != null &&
          ALink.map((item) => (
            <link rel={item.name} href={item.href} size={item.size} />
          ))}

        {/* MS Tags */}
        {MSTags != null &&
          MSTags.map((item) => (
            <meta name={item.name} content={item.content} />
          ))}

        {/* Schema Tags */}
        {MetaSchema != null &&
          MetaSchema.map((item) => (
            <script type={MetaSchemaTag.type}>{item}</script>
          ))}
      </MetaTags>
    </div>
  );
};

export default MetaTagsSeo;
